import * as underscore from "underscore";

/**
  Returns the CSS value for a specific property.
  @returns {string} CSS value.
  @param {HTMLElement} element The HTML dom node you want to get its css property.
  @param {string} property CSS property name.
 */
export function getStyle( element: HTMLElement, property: string ): string {
  const style = window.getComputedStyle( element );
  return style.getPropertyValue( property );
}


/**
 * @param {{ctrlKey:boolean, altKey:boolean, shiftKey:boolean, metaKey:boolean, key:string}} keys 
 * @param {function} callbackFn 
 */
export function bindKeys( 
  element:HTMLElement, keys:{ctrlKey?:boolean, altKey?:boolean, shiftKey?:boolean, metaKey?:boolean, key?:string}, callbackFn:()=>void ) {
  const merge = underscore.extend( DEFAULT_BINDED_KEYS, keys );

  element.addEventListener("keydown", function(e) {
    if( e.ctrlKey == merge.ctrlKey && e.altKey == merge.altKey && e.shiftKey == merge.shiftKey && e.metaKey == merge.metaKey && e.key == merge.key ) {
      e.preventDefault();
      e.stopPropagation();
      callbackFn();
    }
  })
}

/**
 * Binds an event to the body of the DOM such that when you click outside of a dropdown trigger,
 * you can hide thr dropdown
 * HOW TO USE
 * ---------------------------------------------------
 * 1). Define a function that shows the dropdown (E.g. this.dropDownShow() )
 * 
 * 2). Define a function that hides the dropdown (E.g. this.dropDownHide() )
 * 
 * 3). Define a toggle function that hides and shows the drop down on a certain condition
 * 
 * 4). call the "hideDropDownOnOutsideClick" function in the this.dropDownShow() function
 * 
 * The Idea is to immediately bind the onOutSideClick dropdown removal event to the body immediately
 * you show the dropdown menu.  
 * @param dropDownTrigger This is the element that is clicked on that triggers the drop down menu.
 * @param hideMenuFn A function called that handles the removal of the drop down menu.
 */
export function hideDropDownOnOutsideClick( dropDownTrigger:HTMLElement , hideMenuFn:()=>any ) {
  document.body.addEventListener("click", _menuCloser, false)

  function _menuCloser(e:Event) {
    // If what you clicked on is not the trigger for the drop down 
    // or not a child of the trigger for the drop down.
    const youDidntClickOnTheTrigger = e.target != dropDownTrigger
    const whatYouClickedOnIsNotAChildOfTheTrigger = ifChildIsADescendant( e.target as HTMLElement, dropDownTrigger ) == false;
    if( youDidntClickOnTheTrigger && whatYouClickedOnIsNotAChildOfTheTrigger  ) {
      document.body.removeEventListener("click", _menuCloser, false );
      hideMenuFn();
    }
  }
}

/**
 * Checks if an element (child:HTMLElement) is a child/descendant of another element (parent:HTMLElement)
 */
export function ifChildIsADescendant( child:HTMLElement, parent:HTMLElement ):boolean {
  let node = child.parentNode;
  while( node != null ) {
    if( node == parent ) {
      return true
    }
    node = node.parentNode;
  }
  return false;
}




const DEFAULT_BINDED_KEYS = {
  ctrlKey: false, 
  altKey: false, 
  shiftKey: false, 
  metaKey: false, 
  key: "",
}


