import connectToDB from '../schema';
import * as quill from "quill";
import Delta from "quill-delta";
import helpers from "../helpers.js";
import * as lf from "lovefield";



export const username = "michaelolof";

export type Note = {
  title: string,
  textContent: string,
  deltaContent: quill.DeltaStatic,
  settings: object,
}

export enum NoteState {
  New,
  Old
}

export function createNewNote(note:Note): Promise<Object[]>  {
  return connectToDB
  .then( function(db) {
    let notes = db.getSchema().table("Notes");
    let notesRow = notes.createRow({
      "id": 0,
      "author": username,
      "title": note.title,
      "textContent": note.textContent,
      "deltaContent": note.deltaContent,
      "settings": note.settings,
      "createdAt": new Date(),
      "updatedAt": new Date(),
    });
    return db.insertOrReplace().into(notes).values([ notesRow ]).exec();
  })
}

export function updateNote( noteId:number, note:Note ): Promise<any> {
  return connectToDB
  .then(function(db) {
    let noteRow = db.getSchema().table("Notes");
    
    return db
    .update(noteRow)
    .set( noteRow.title, note.title )
    .set( noteRow.textContent, note.textContent )
    .set( noteRow.deltaContent, note.deltaContent )
    .set( noteRow.settings, note.settings )
    .set( noteRow.updatedAt, new Date() )
    .where( noteRow["id"].eq( noteId ) )
    .exec();
  });
}

/**
 * Fetch notes from the database.
 * @param {number} pageNo The current page in display. 
 * @param {number} amountPerPage Number of notes to fetch at a particular page.. 
 */
export function fetchNotes( pageNo:number, amountPerPage:number ): Promise<Object[]> {
  return connectToDB
  .then( function(db) {
    let notes = db.getSchema().table("Notes");
    let offset = helpers.calculateOffset( pageNo, amountPerPage );

    return db
    .select( notes.id, notes.title, notes.author, notes.updatedAt )
    .from( notes )
    .limit( amountPerPage )
    .skip( offset )
    .exec();
  })
}


export function fetchRecentNotes( pageNo:number, amountPerPage:number ): Promise<any> {
  return connectToDB
  .then( function(db) {
    let noteTable = db.getSchema().table("Notes");
    let offset = helpers.calculateOffset( pageNo, amountPerPage );
    
    const values = db
    .select( noteTable.id, noteTable.title, noteTable.author, noteTable.updatedAt )
    .from( noteTable )
    .limit( amountPerPage )
    .skip( offset )
    .orderBy( noteTable.updatedAt, lf.Order.DESC )
    .exec();

    return values;
  })
}

export function fetchNote( noteId:number ): Promise<any> {
  return connectToDB
  .then(function(db) {
    let noteTable = db.getSchema().table("Notes");
    const values = db
    .select( noteTable.id, noteTable.title, noteTable.textContent,
             noteTable.deltaContent, noteTable.updatedAt, noteTable.settings )
    .from( noteTable )
    .where( noteTable["id"].eq( noteId) )
    .exec();
    return values;
  })
}

export function deleteNote( noteId:number ): Promise<any> {
  return connectToDB
  .then(function(db) {
    let noteTable = db.getSchema().table("Notes");
    return db
    .delete()
    .from( noteTable )
    .where( noteTable.id.eq( noteId ))
    .exec();
  });
}

export async function searchNotes(query:string): Promise<Object[]> {
  const queryPattern = new RegExp( query, "i" );
  const db =  await connectToDB
  const noteTable = db.getSchema().table("Notes");

  // Define Selection
  const initialResults = await db.select( noteTable.id, noteTable.title, noteTable.author, noteTable.updatedAt, noteTable.textContent )
                        .from( noteTable )
                        .where( noteTable.title.match( queryPattern ) )
                        .exec();


  const secondaryResults = await db.select( noteTable.id, noteTable.title, noteTable.author, noteTable.updatedAt, noteTable.textContent )
                          .from( noteTable )
                          .where( noteTable.textContent.match( queryPattern ) )
                          .exec();
  
  return [ ...initialResults, ...secondaryResults ];
}





