import * as Quill from 'quill';
import Delta from "quill";
import quillJS from "./type-maker";





/**
 * Inspects a selection's format.
 * @param quill - instance of the 'Quill' class i.e: new Quill(...)
 * @param range - A range of the current selection, gotten from => (new Quill(...)).getSelection()
 * @param formatIndentifier - A Quill registered identifier for the format to inspect (E.g. "align", "code-block", "color" e.t.c)
 */
export function inspectFormat( quill: Quill.Quill, range: Quill.RangeStatic, formatIndentifier:string ): {format:Quill.Formats, formatExist:boolean} {
  let format = quill.getFormat( range.index, range.length );
  let formatExist = false;
  if( format[ formatIndentifier ] != undefined ) formatExist = true;
    return {
    format,
    formatExist
  }        
}

export function extractFormat( format:Quill.Formats, formatIdentifier:string ):any {
  return format[ formatIdentifier ];
}

/**
 * Removes a specific format type from a given format object.
 * @returns {void} 
 * @param quill - instance of the 'Quill' class i.e: new Quill(...)
 * @param range - A range of the current selection, gotten from => (new Quill(...)).getSelection()
 * @param format - An object representing all the Quill registered formats for a given selection (E.g. { align:"center", code-block:true, color:"blue"} )
 * @param formatIndentifier - A Quill registered identifier for the format to inspect (E.g. "align", "code-block", "color" e.t.c)
 */
export function removeFormat( quill: Quill.Quill, range:Quill.RangeStatic, format:Quill.Formats, formatIndentifier:string ): void {
  delete format[ formatIndentifier ];
  quill.removeFormat( range.index, range.length );
  quill.formatText( range.index, range.length, format );
  quill.formatLine( range.index, range.length, format );
}

export function getAllRangesForQueryInPool( query:string, pool:string ):Quill.RangeStatic[] {
  let rtn:Quill.RangeStatic[] = [];
  const length = query.length;
  // Get the first occurence of the query
  let index = pool.indexOf( query, 0 );
  // Acount for when there is no match at all
  if( index == -1 ) {
    // If there is no match at all breakout of the function by
    // returning an empty array.
    return rtn;
  }
  
  // Push first occurence to the array
  rtn.push( quillJS.make.Range( index, length ) ) 

  // If there is a match we want to continue where we stopped searching.
  // As long
  while( index != -1 ) {
    // find the NEXT occurence of the query.
    index = pool.indexOf( query, index + length );
    // Check if the occurence doesn't exist anymore and break out of the loop
    if( index == - 1 ) break;
    // If there is still an occurence define a new range and push it to the array.
    rtn.push( quillJS.make.Range( index, length ) )
  }
  return rtn;
}





