import { Icon } from '../../utilities/Icon';
import Vue from "vue";
import { Component } from "vue-property-decorator";
import * as Quill from "quill"
import * as utilities from "../../../modules/quill/helper";
import * as htmlElements from "../../../modules/HTMLElements";
import * as Mousetrap from 'mousetrap';


@Component({
  template: `
  <span class="icon-blk" ref="outdent">
    <Icon type="icon icon-outdent" width="100px">Outdent<br>(Alt + &#8592)</Icon>
  </span>`,

  components: {
    Icon,
  }
})

export class Outdent extends Vue {
  // Props
  //--------------------------------------------------------------------------------------------------------

  // Data Properties
  //--------------------------------------------------------------------------------------------------------
  indentStart: number = 0;

  // Computed Properties
  //--------------------------------------------------------------------------------------------------------

  // Methods
  //--------------------------------------------------------------------------------------------------------
  setup(quill: Quill.Quill, editorPage:HTMLElement) {
    const vm = this;
    const outdent = <HTMLElement>this.$refs.outdent;

    function _helper() {
      const selection = quill.getSelection();
      vm.outdent(quill, selection);      
    }

    outdent.addEventListener("click", _helper );
    
    Mousetrap( editorPage ).bind(["alt+left"], function() {
      _helper();
      return false;
    })
  }

  outdent(quill: Quill.Quill, range: Quill.RangeStatic) {
    if (range == null || range == undefined) return;
    const { indent } = quill.getFormat(range);
    if (indent == undefined) return;
    if (indent == this.indentStart) return;
    quill.formatLine(range.index, range.length, "indent", indent - 1);
  }


  // Watchers
  //--------------------------------------------------------------------------------------------------------

  // Life cycle hooks
  //--------------------------------------------------------------------------------------------------------
}



