import Vue from "vue";
import { Component, Prop } from "vue-property-decorator";

@Component({
  template: `
    <div class="list-with-icon" v-bind:title="titleAttribute">
      <i v-bind:class="icon"></i>
      <span><slot></slot></span>
    </div>`,

})

export class ListWithIcon extends Vue {
  // Props
  //--------------------------------------------------------------------------------------------------------
  /**
   * Vue Prop: defines the class of the icon to be displayed.
   */  
  @Prop public icon: string;

  /**
   * Vue Prop: Defines the content of the HTML title attribute.
   */
  @Prop public titleAttribute: string;

  // Data Properties
  //--------------------------------------------------------------------------------------------------------

  // Computed Properties
  //--------------------------------------------------------------------------------------------------------

  // Methods
  //--------------------------------------------------------------------------------------------------------

  // Watchers
  //--------------------------------------------------------------------------------------------------------

  // Life cycle hooks
  //--------------------------------------------------------------------------------------------------------
}



