import Vue from "vue";
import { Component, Watch } from "vue-property-decorator";
import { Note } from './Note';
import { ButtonComponent } from '../../utilities/ButtonComponent';


@Component({
  template: `
  <div class="notes-library">
    <div class="note-blk" v-for="note in notes">
      <Note v-bind:title="note.title" v-bind:id="note.id" v-bind:updated="note.updatedAt" :key="note.id" />
    </div>
    <div class="show-more button-component-blk"
      @click="increasePageNo">
      <ButtonComponent v-show="databaseHasMoreNotes" color="silver">Show More</ButtonComponent>
    </div>      
  </div>`,
    
  components: {
    Note,
    ButtonComponent,
  }
})

export class NotesLibrary extends Vue {
  // Props
  //---------------------------------------------------------------------------------------------
  
  // Data Properties
  //---------------------------------------------------------------------------------------------

  // Computed Properties
  //---------------------------------------------------------------------------------------------
  get notes() {
    return this.$store.getters["notesLibrary/libraryNotes"];
  }

  get pageNo() {
    return this.$store.getters["notesLibrary/pageNo"];
  }

  get databaseHasMoreNotes(): boolean {
    return this.$store.getters["notesLibrary/databaseHasMoreNotes"]
  }

  // Watchers
  //---------------------------------------------------------------------------------------------
  @Watch("pageNo")
  onPageNoChanged() {
    this.$store.dispatch("notesLibrary/fetchNotesFromDatabase")
  }


  // Methods
  //---------------------------------------------------------------------------------------------
  increasePageNo() {
    this.$store.commit("notesLibrary/increasePageNo");
  }

  // Life cycle hooks
  //---------------------------------------------------------------------------------------------
}


