import Vue from "vue";
import { Component, Prop } from "vue-property-decorator";

@Component({
  template: `
    <div class="button"
      v-bind:color="color"
      v-bind:commit="commit"
      @click="handleClickEvent">
      <slot></slot>
    </div>`,

  components: {

  },
})

export class ButtonComponent extends Vue {
  // Props
  //---------------------------------------------------------------------------------------------------
  @Prop()
  color :string;

  @Prop()
  commit: string;

  @Prop()
  onClickMethod: string;

  // Data Properties
  //---------------------------------------------------------------------------------------------------

  // Computed Properties
  //---------------------------------------------------------------------------------------------------

  // Methods
  //---------------------------------------------------------------------------------------------------
  handleClickEvent() {
    if( this.commit == undefined ) {
      return
    }
    this.$store.commit( this.commit );
  };


  // Watchers
  //---------------------------------------------------------------------------------------------------

  // Life cycle hooks
  //---------------------------------------------------------------------------------------------------

}



