import Vue from "vue";
import { Component, Prop, Watch } from 'vue-property-decorator';
import { DropDownMenuContainer } from './drop-down-menu/DropDownMenuContainer';
import * as htmlElement from "../../modules/HTMLElements";

@Component({
  template: `
  <div class="select-with-dropdown">
    <div class="select-blk" @click="toggleDropDown" ref="selectBlk">
      <span class="label">{{ value }}</span>
      <i class="icon icon-caret-down"></i>
    </div>
    <DropDownMenuContainer
      v-bind:style="styles" 
      v-show="menuIsShown"
      v-bind:width="width"
      v-bind:height="height">

      <slot></slot>
      
    </DropDownMenuContainer>

  </div>`,
    
  components: {
    DropDownMenuContainer
  }
})

export class SelectWithDropDown extends Vue {
  // Props
  //---------------------------------------------------------------------------------------------
  @Prop width:string
  @Prop height:string
  @Prop value:string

  // Data Properties
  //---------------------------------------------------------------------------------------------
  menuIsShown = false;
  styles = {left: "0px"}  

  // Computed Properties
  //---------------------------------------------------------------------------------------------
  
  // Methods
  //---------------------------------------------------------------------------------------------
  toggleDropDown() {
    if( this.menuIsShown ) {
      this.hide();
    } else {
      this.show();
    }
  }

  private hide() {
    this.menuIsShown = false;
  }

  private show() {
    this.menuIsShown = true;
    htmlElement.hideDropDownOnOutsideClick( this.$refs.selectBlk as any, this.hide );
  }

  updatePositionStyles() {
    const selectBlk = <HTMLElement> this.$refs.selectBlk
    const styles = {
      left: selectBlk.offsetLeft + "px",
    }
    this.styles = styles;
  }

  // Watchers
  //---------------------------------------------------------------------------------------------
  @Watch("menuIsShown")
  onMenuIsShownChanged() {
    this.updatePositionStyles();
  }

  // Life cycle hooks
  //---------------------------------------------------------------------------------------------
  mounted() {
    this.updatePositionStyles();
  }
}


