import { VuexClass, HasGetter, extractVuexModule } from 'vuex-class-module';
//import { Module, Getters } from '../modules/vuex-decorators';

// Vuex Module Type Definitions
//-----------------------------------------------------------------------------------------------------
type Context = {
  state: State
  getters: Getters
  commit(mutation: string, payload: any): void
  dispatch(action: string, payload: any): void
}

type State = {
  username: string,
  firstname: string,
  lastname: string,
}

type Getters = {
  username: string,
  firstname: string,
  lastname: string,
}

@VuexClass({
  modules: {}
})

class General {
 // S T A T E
 //---------------------------------------------------------------------------------------------------------
  @HasGetter username = "michaelolof";
  @HasGetter firstname = "Michael";
  @HasGetter lastname = "Olofinjana";
 //---------------------------------------------------------------------------------------------------------

}


const general = extractVuexModule( General );
export default general;


