import Vue from "vue";
import { Component } from "vue-property-decorator";
import { Icon } from '../../utilities/Icon';
import * as Quill from "quill"
import * as utilities from "../../../modules/quill/helper";
import * as htmlElements from "../../../modules/HTMLElements";
import * as Mousetrap from 'mousetrap';

@Component({
  template: `
    <span class="icon-blk" ref="codeComponent">
      <Icon type="icon icon-code" width="100px">Code <br> (Ctrl/Cmd + K)</Icon>
    </span>`,

  components: {
    Icon,
  }
})

export class CodeComponent extends Vue {
  // Props
  //--------------------------------------------------------------------------------------------------------

  // Data Properties
  //--------------------------------------------------------------------------------------------------------

  // Computed Properties
  //--------------------------------------------------------------------------------------------------------

  // Methods
  //--------------------------------------------------------------------------------------------------------
  setup(quill: Quill.Quill, editorPage: HTMLElement) {
    const codeComponent = <HTMLElement>this.$refs.codeComponent;
    let range = undefined;

    function _helper() {
      range = quill.getSelection();
      const { format, formatExist } = utilities.inspectFormat(quill, range, "code-block");

      if (formatExist) {
        utilities.removeFormat(quill, range, format, "code-block");
        return;
      }

      quill.formatLine(range.index, range.length, "code-block", true);
    }

    codeComponent.addEventListener("click", _helper);

    Mousetrap( editorPage ).bind(["command+k", "ctrl+k"], () => { 
      _helper();
      return false; 
    });
    
  }

  // Watchers
  //--------------------------------------------------------------------------------------------------------

  // Life cycle hooks
  //--------------------------------------------------------------------------------------------------------
  mounted() {
    hljs.configure({
      languages: ["javascript", "go", "c#", "php", "erlang", "java", "smalltalk", "d", "python"]
    });
  }
}



