import * as notes from '../../database/queries/notes';
import notesMainSection from './notesMainSection';
import notesSideSection from './notesSideSection';
import { Action, VuexClass, Getter, Mutation, HasGetter, extractVuexModule } from "vuex-class-module"
import * as Vue from 'vue';


// Vuex Module Type Definitions
//-----------------------------------------------------------------------------------------------------
type Context = {
  state: State
  getters: Getters
  commit(mutation: string, payload?: any): Promise<any>
  dispatch(action: string, payload?: any): Promise<any>
}

type State = {
  libraryNotes: [LibraryNote],
  pageNo: number,
  databaseHasMoreNotes: boolean,
  temporaryNotesStore:[LibraryNote],
  libraryContainsSearchResults:boolean,
}

type Getters = {
  libraryNotes: [LibraryNote],
  pageNo: number,
  databaseHasMoreNotes: boolean,
}

export type LibraryNote = {
  id: number,
  author: string,
  title: string,
  updatedAt: Date
}

@VuexClass({
  modules: {
    notesMainSection,
    notesSideSection,
  }
})

class NotesLibrary {
  // S T A T E
  //------------------------------------------------------------------------------------------------------------
  @HasGetter libraryNotes:Object[] = [];
  @HasGetter pageNo: number = 1;
  @HasGetter databaseHasMoreNotes: boolean = true;
  temporaryNotesStore:any = [];
  libraryContainsSearchResults:boolean = false;


  // M U T A T I O N S
  //-------------------------------------------------------------------------------------------------------------
  @Mutation pushNoteToLibrary(state:State, note:LibraryNote) {
    // If note doesn't exist in the library.
    // push note to the top of the list
    state.libraryNotes.splice( 0, 0, note );
  }

  @Mutation increasePageNo(state:State) {
    state.pageNo = state.pageNo + 1;
  }

  @Mutation restoreLibrary(state:State) {
    if( state.temporaryNotesStore.length > 0 ) {
      state.libraryNotes = state.temporaryNotesStore;
    }
  }

  // A C T I O N S
  //--------------------------------------------------------------------------------------------------------------
  @Action async refreshNotesFromDatabase( context:Context): Promise<[LibraryNote]> {
    const noOfPages = context.getters.pageNo * constant.AMOUNT_PER_PAGE;
    const values: [LibraryNote] = await notes.fetchRecentNotes( 1, noOfPages );
    // When you save for the first time, you want to automatically display the saved note in the notes side section.
    const sideSectionView: string = context.getters["notesSideSection/currentView"] 

    if( sideSectionView != "Notes" ) {
      context.commit("notesSideSection/setCurrentView", "Notes" );
    }

    context.state.libraryNotes = values;
    return context.state.libraryNotes;

  }

  @Action fetchNotesFromDatabase(context: Context): Promise<[LibraryNote]> {
    // Check if there are no more notes in the database and break out.
    if( context.getters.databaseHasMoreNotes == false ) {
      return
    }
    
    // If Library contains search results and we want to fetch notes.
    // This will only happen if we have used the search bar and it has results.
    // In that case if the show more button is called, refresh notes.
    if( context.state.libraryContainsSearchResults ) {
      context.dispatch("refreshNotesFromDatabase");
      return;
    }
    
    return notes
    .fetchRecentNotes( context.getters.pageNo, constant.AMOUNT_PER_PAGE )
    .then(function (values:[LibraryNote]) {
      // Check if there is a value returned. update it in the library.
      // And if there is no value returned update the state of the library 
      // to reflect it has no more notes in the database
      if( values.length > 0 ) {
        context.state.libraryNotes = <[LibraryNote]> [ ...context.state.libraryNotes, ...values ];
        // Update Temporary Store
        context.state.temporaryNotesStore = context.state.libraryNotes;
      } else  {
        // Update state of library.
        context.state.databaseHasMoreNotes = false;
      }
      return context.state.libraryNotes;
    })
  }

  @Action loadLibraryNotes(context:Context) {
    context
    .dispatch("fetchNotesFromDatabase")
    .then( (values:[LibraryNote] ) => {   
      if (values.length > 0) {
        context.commit("notesMainSection/setCurrentView", "NotesLibrary");
        context.commit("notesSideSection/setCurrentView", "Notes");
      }
    })
  }

  @Action deleteNoteFromDatabase(context:Context, noteId:number) {
    notes
    // Delete Note from Database
    .deleteNote( noteId )
    .then(() => {
      // Go through the notes in the library and check if
      // deleted note is there to remove it.
      let i = 0;
      for( let note of context.state.libraryNotes ) {
        let noteIndex = i;
        if( note.id == noteId ) {
          context.state.libraryNotes.splice( noteIndex, 1 );
          break;
        }
        i = i + 1;
      }
    })
  }

  @Action searchNotes(context:Context, query:string) {
    notes
    .searchNotes( query )
    .then(function( values:[LibraryNote] ) {
      // Update Library with fetched results.
      context.state.libraryNotes = values;
      // Update search result state in the library.
      context.state.libraryContainsSearchResults = true;
    })
  }

}


const constant = {
  AMOUNT_PER_PAGE: 20,
}

const notesLibrary = extractVuexModule( NotesLibrary )

export default notesLibrary;


