import Vue from "vue";
import { Component } from "vue-property-decorator";
import { Icon } from "../../utilities/Icon";
import * as Quill from "quill";


@Component({
  template: `
    <span class="icon-blk" ref="bold">
      <Icon type="icon icon-bold" width="100px">Bold <br>(Ctrl/Cmd + B)</Icon>
    </span>`,

    components: {
      Icon,
    }
})

export class Bold extends Vue {
  // Props
  //--------------------------------------------------------------------------------------------------------

  // Data Properties
  //--------------------------------------------------------------------------------------------------------

  // Computed Properties
  //--------------------------------------------------------------------------------------------------------

  // Methods
  //--------------------------------------------------------------------------------------------------------
  setup( quill: Quill.Quill, editorPage: HTMLElement ):void {
    const bold = <HTMLElement> this.$refs.bold;
    bold.addEventListener("click", function() {
      const selection = quill.getSelection();
      quill.formatText( selection.index, selection.length, "bold", true );
    });
  }

  // Watchers
  //--------------------------------------------------------------------------------------------------------

  // Life cycle hooks
  //--------------------------------------------------------------------------------------------------------
}



