import Vue from "vue";
import { Component, Prop } from "vue-property-decorator";

@Component({
  template: `
    <span class="legend-tag" v-show="isShown">
      <span class="legend-content"><slot></slot></span>
    </span>`,

    components: {
    }
})

export class LegendTag extends Vue {
  // Props
  //--------------------------------------------------------------------------------------------------------
  @Prop({ default: "center" })
  dir: string;

  // Data Properties
  //--------------------------------------------------------------------------------------------------------
  isShown: boolean = false;
  // Computed Properties
  //--------------------------------------------------------------------------------------------------------

  // Methods
  //--------------------------------------------------------------------------------------------------------
  show() {
    this.isShown = true;
  }

  hide() {
    this.isShown = false;
  }

  // Watchers
  //--------------------------------------------------------------------------------------------------------

  // Life cycle hooks
  //--------------------------------------------------------------------------------------------------------
}



