import Vue from "vue";
import { Component } from "vue-property-decorator";
import { FormatSection } from './FormatSection';
import { NotesEditor } from "./NotesEditor";
import { SearchInput } from '../../utilities/SearchInput';
import * as Quill from "quill";
import { getAllRangesForQueryInPool, inspectFormat, removeFormat } from '../../../modules/quill/helper';
import * as types from '../../../modules/quill/type-maker';
import Delta from "quill-delta";
//import { findAndReplaceDOMTextLibrary as selection  } from "../../../modules/findInSelection"
import { findAndReplaceDOMTextLibrary as selection  } from "../../../modules/findInSelection"

@Component({
  template: `
  <span class="search-input-blk">
    <SearchInput>
      <input type="text" @focus="refreshEditorContent" @keyup="findInSelection" v-model="query" placeholder="Find..."/>
    </SearchInput>
  </span>`,

  components: {
    SearchInput,
  }
})

export class FindInEditor extends Vue {
  // Props
  //---------------------------------------------------------------------------------------------

  // Data Properties
  //---------------------------------------------------------------------------------------------
  query: string = "";
  // defines all the current text ranges found.
  editorContent: string = "";
  readonly EDITOR_CONTENT_LIMIT = 999999;


  // Computed Properties
  //---------------------------------------------------------------------------------------------
  get formatSection(): FormatSection {
    return <FormatSection>this.$parent
  }

  get quill(): Quill.Quill {
    return this.formatSection.getQuillEditorInstance();
  }

  get quillEditorDOM(): HTMLElement {
    return document.querySelector(".ql-editor") as HTMLElement;
  }

  get quillEditorParent(): HTMLElement {
    return document.querySelector(".editor.ql-container") as HTMLElement
  }

  // Methods
  //---------------------------------------------------------------------------------------------
  refreshEditorContent() {
    this.editorContent = this.quillEditorDOM.innerHTML;
  }

  

  findInSelection(e: KeyboardEvent) {
    const query = this.query.trim();

    selection.revert(this.query, this.quillEditorDOM);

    // If enter is pressed
    if (e.which == 13) {
      selection.find(this.query, this.quillEditorDOM);
    }
  }

  findUsingFindAndReplaceDOMTextAPI() {
    const vm = this;
    const query = vm.query.trim();

    if (query.length == 0) return;

    // Create a regex match
    const match = new RegExp(query, "ig")
    const highlighted = `<span class="find">${query}</span>`
    const replaced = vm.editorContent.replace(match, highlighted);

    if (replaced.length < vm.EDITOR_CONTENT_LIMIT) {
      vm.quillEditorDOM.innerHTML = replaced;
      return
    }

    console.log(replaced);

    vm.quill.clipboard.dangerouslyPasteHTML(replaced);

  }

  revertUsingFindAndReplaceDOMTextAPI() {

  }

  // Watchers
  //---------------------------------------------------------------------------------------------

  // Life cycle hooks
  //---------------------------------------------------------------------------------------------
  mounted() { }


}


