import Vue from "vue";
import { Component } from "vue-property-decorator";
import { SelectWithDropDown } from '../../utilities/SelectWithDropDown';
import { DropDownMenu } from '../../utilities/drop-down-menu/DropDownMenu';
import * as Quill from 'quill';
import { Optional } from 'moo-types';
import * as helper from "../../../modules/quill/helper";
import * as Mousetrap from 'mousetrap';
import randomString from 'randomstring';


@Component({
  template: `
  <span class="icon-blk">
    <SelectWithDropDown
      width="250px"
      height="auto"
      v-bind:value="currentSelection">
      
      <DropDownMenu><h1 @click="formatHeaderOne">{{ selections[0] }}</h1></DropDownMenu>
      <DropDownMenu><h2 @click="formatHeaderTwo">{{ selections[1] }}</h2></DropDownMenu>
      <DropDownMenu><h3 @click="formatHeaderThree">{{ selections[2] }}</h3></DropDownMenu>
      <DropDownMenu><span @click="removeHeaderFormat">{{ selections[3] }}</span></DropDownMenu>

    </SelectWithDropDown>
  </span>
  `,

  components: {
    SelectWithDropDown,
    DropDownMenu,
  }
})

export class HeaderComponent extends Vue {
  // Props
  //--------------------------------------------------------------------------------------------------------

  // Data Properties
  //--------------------------------------------------------------------------------------------------------
  selections = [
    "Heading",
    "Sub Heading",
    "Mini Heading",
    "Normal"
  ]
  currentSelection = this.selections[3];
  quill = Optional.none<Quill.Quill>();
  editorPage = Optional.none<HTMLElement>();

  // Computed Properties
  //--------------------------------------------------------------------------------------------------------


  // Methods
  //--------------------------------------------------------------------------------------------------------
  setup(quill:Quill.Quill, editorPage:HTMLElement ) {
    const vm = this;
    vm.quill = Optional.some(quill);
    vm.editorPage = Optional.some(editorPage);

    Mousetrap( editorPage ).bind(["command+up", "ctrl+up"], function() {
      vm.increaseFontSize();
      return false;
    })

    Mousetrap( editorPage ).bind(["command+down", "ctrl+down"], function() {
      vm.decreaseFontSize();
      return false;
    })
  }

  increaseFontSize() {
    this.stepThroughFontSize( FontSizeStepper.Increase );
  }

  decreaseFontSize() {
    this.stepThroughFontSize( FontSizeStepper.Decrease );
  }

  formatHeaderOne() {
    if ( this.quill.ifExist() == false ) return;
    
    const quill = this.quill.force( 1 as any );
    const range = quill.getSelection();
    if ( range == undefined ) return;

    quill.formatLine( range.index, range.length, "header", "1" );
    this.currentSelection = this.selections[0];
  }

  formatHeaderTwo() {
    if( this.quill.ifExist() == false ) return;

    const quill = this.quill.force( 1 as any );
    const range = quill.getSelection();
    if ( range == undefined ) return;
    
    quill.formatLine( range.index, range.length, "header", "2" );
    this.currentSelection = this.selections[1];
  }

  formatHeaderThree() {
    if( this.quill.ifExist() == false ) return;

    const quill = this.quill.force( 1 as any );
    const range = quill.getSelection();
    if ( range == undefined ) return;
    
    quill.formatLine( range.index, range.length, "header", "3" );
    this.currentSelection = this.selections[2];    
  }

  removeHeaderFormat() {
    if( this.quill.ifExist() == false ) return;

    const quill = this.quill.force( 1 as any );
    const range = quill.getSelection();
    if ( range == undefined ) return;
    
    const { format, formatExist } = helper.inspectFormat( quill, range, "header" );
    if( formatExist ) {
      helper.removeFormat( quill, range, format, "header" );
      this.currentSelection = this.selections[ 3 ];
    }
  }

  private stepThroughFontSize( stepper:FontSizeStepper ) {
    const vm = this;

    // Check if quill instance has been defined, if not breakout.
    if( this.quill.ifExist() == false ) return;

    const quill = this.quill.force( 1 as any );
    const range = quill.getSelection();
    
    // Determine if editor is in focus, if not break out.
    if( range == undefined ) return;

    // Try and obtain the current header value in the selection.
    const optValue = this.obtainHeaderFormat( quill, range );

    // If there is no header format...
    //------------------------------------------------------------------------------------
    if( optValue.ifExist() == false ) {
      // ...And we are increasing for the first time
      if( stepper == FontSizeStepper.Increase ) {
        quill.formatLine( range.index, range.length, "header", constants.MIN_HEADER_FONT_VALUE );
      }
      // break out of the function.
      return;
    }

    // If there's already a header format...
    //--------------------------------------------------------
    // ...then obtain the current header format.
    let value = optValue.force(0);
    // ...If we're increasing font size.
    if( stepper == FontSizeStepper.Increase ) {
      if( value == constants.MAX_HEADER_FONT_VALUE ) return; // We can't increase font size higher than this.
      value = value - 1; // We do a subtraction because 1 is the highest, 2 is the 2nd highest, 3 the 3rd highest etc.
    } 
    
    // ...If we're decreasing font size.
    else if( stepper == FontSizeStepper.Decrease ) {
      if( value == constants.MIN_HEADER_FONT_VALUE ) { // if we are at the lowest header format...
        vm.removeHeaderFormat(); // This will set the font size back to normal.
      }
      value = value + 1; // We do an addition because 1 is the highest, 2 is the 2nd highest, 3 the 3rd highest etc.
    }

    quill.formatLine(range.index, range.length, "header", value );

  }
  
  private obtainHeaderFormat(quill:Quill.Quill, range:Quill.RangeStatic):Optional<number> {
    const { format, formatExist } = helper.inspectFormat( quill, range, "header" );
    if( formatExist ) {
      const formatValue = helper.extractFormat( format, "header" );
      return Optional.some( formatValue );
    } else {
      return Optional.none<number>();
    }

  }

  // Watchers
  //--------------------------------------------------------------------------------------------------------

  // Life cycle hooks
  //--------------------------------------------------------------------------------------------------------
}


enum FontSizeStepper {
  Increase,
  Decrease,  
}

const constants = {
  MAX_HEADER_FONT_VALUE: 1, // i.e. <h1></h1>
  MIN_HEADER_FONT_VALUE: 3, // i.e. <h3></h3>
} 



