/**
 * Simulates a click event on an element.
 * @returns {void} 
 * @param {object} element The DOM element to fire event on. 
 * @param {string} eventType The string representation of the event to be fired. 
 */
function eventFire( element, eventType ) {
  if( element.fireEvent ) {
    element.fireEvent("on" + eventType)
  } else {
    let eventObject = document.createEvent("Events");
    eventObject.initEvent( eventType, true, false );
    element.dispatchEvent( eventObject );
  }
}


export default {
  eventFire,
}


