import Vue from "vue";
import { App } from "./components/App";
import store from "./store/store";
import * as quillRegistry from "./modules/quill/registry"

// Load Imported Modules.
quillRegistry;


new Vue({
  el: "#app",
  store,
  components: {
    app: App,
  },
})



