import Vue from "vue";
import { Component } from "vue-property-decorator";
import { Icon } from '../../utilities/Icon';
import * as Quill from "quill";
import * as utilities from "../../../modules/quill/helper";
import * as htmlElements from "../../../modules/HTMLElements";
import * as Mousetrap from 'mousetrap';


@Component({
  template: `
  <span class="icon-blk" ref="alignJustify">
    <Icon type="icon icon-align-justify" width="60px">
      Justify
      <br />
      (Alt + J)
    </Icon>
  </span>`,

  components: {
    Icon
  }
})

export class AlignJustify extends Vue {
  // Props
  //--------------------------------------------------------------------------------------------------------

  // Data Properties
  //--------------------------------------------------------------------------------------------------------

  // Computed Properties
  //--------------------------------------------------------------------------------------------------------

  // Methods
  //--------------------------------------------------------------------------------------------------------
  setup(quill: Quill.Quill, editorPage:HTMLElement) {
    const vm = this;
    const alignJustify = <HTMLElement> this.$refs.alignJustify;

    function _helper() {
      const selection = quill.getSelection();
      vm.toggleAlignJustify(quill, selection);      
    }

    alignJustify.addEventListener("click", _helper );
    
    Mousetrap( editorPage ).bind(["alt+j"], () => {
      _helper();
      return false;
    })
  }

  toggleAlignJustify(quill :Quill.Quill, range: Quill.RangeStatic) {
    let format = quill.getFormat(range.index, range.length);
    if (format.align == "justify") {
      delete format.align
      quill.removeFormat(range.index, range.length);
      quill.formatLine(range.index, range.length, format);
      return;
    }
    quill.formatLine(range.index, range.length, "align", "justify");
  }

  // Watchers
  //--------------------------------------------------------------------------------------------------------

  // Life cycle hooks
  //--------------------------------------------------------------------------------------------------------
}



