import Vue from "vue";
import { Component } from "vue-property-decorator";
import { AppContainer } from './AppContainer';
import { OverlayContainer } from './OverlayContainer';

@Component({
  template: `
    <div>
      <AppContainer />
      <OverlayContainer v-show="isShown" />
    </div>`,
  components: {
    AppContainer,
    OverlayContainer,
  }
})

export class App extends Vue {

  // Computed Properties.
  get isShown(): boolean {
    //return true;
    return this.$store.getters["overlayContainer/isShown"];
  }
  
}



