import Vue from "vue";
import { Component } from "vue-property-decorator";
import { ButtonComponent } from '../../utilities/ButtonComponent';

@Component({
  template: `
    <div class="link-overlay">
      <div class="link-overlay--header">Link</div>
      <div class="link-overlay--body">
        <input type="text" v-model="link" autofocus="true" />
      </div>
      <div class="link-overlay--footer">
        <span class="button-component-blk">
          <ButtonComponent color="silver" commit="overlayContainer/close">Cancel</ButtonComponent>
        </span>
        <span class="button-component-blk float-right" @click="saveLink">
          <ButtonComponent color="silver">Save</ButtonComponent>
        </span>
      </div>
    </div>`,

    components: {
      ButtonComponent,
    }
})

export class LinkOverlay extends Vue {
  // Props
  //--------------------------------------------------------------------------------------------------------

  // Data Properties
  //--------------------------------------------------------------------------------------------------------
  link: string = "http://flasq.co";

  // Computed Properties
  //--------------------------------------------------------------------------------------------------------

  // Methods
  //--------------------------------------------------------------------------------------------------------
  saveLink() {
    this.$root.$emit("notesEditorLinkSet", this.link );
    this.$store.commit("overlayContainer/close");
  }

  // Watchers
  //--------------------------------------------------------------------------------------------------------

  // Life cycle hooks
  //--------------------------------------------------------------------------------------------------------
}



