import { ListWithIcon } from '../../utilities/ListWithIcon';
import Vue from "vue";
import { Component, Watch } from "vue-property-decorator";
import { SideSection } from '../SideSection';


@Component({
  template: `
    <div class="section-blk--section-body" ref="notesSectionBody">
      <span class="icon-list-blk">
        <span v-for="note in notes" :key="note.id" @click="openNoteInEditor(note.id)">
          <ListWithIcon v-bind:titleAttribute="note.title" icon="icon-sticky-note">{{ note.title }}</ListWithIcon>
        </span>
      </span>
    </div>`,
    
  components: {
    ListWithIcon,
    SideSection,
  }
})

export class Notes extends Vue {
  // Props
  //---------------------------------------------------------------------------------------------
  
  // Data Properties
  //---------------------------------------------------------------------------------------------

  // Computed Properties
  //---------------------------------------------------------------------------------------------  
  get notes() {
    return this.$store.getters["notesLibrary/libraryNotes"];
  }

  get notesSideSection():HTMLElement {
    return this.$parent.$el;
  }

  get sectionBlkBody():HTMLElement {
    return this.$refs.notesSectionBody as HTMLElement;
  }

  // Watchers
  //---------------------------------------------------------------------------------------------
  @Watch("notes")
  onNotesChanged() {}  

  // Methods
  //---------------------------------------------------------------------------------------------
  private openNoteInEditor( noteId:number ) {
    this.$store.dispatch("notesEditor/openNoteInEditor", noteId );
  }
   
  private setSectionBlkBodyHeight() {
    const height = this.notesSideSection.offsetHeight - 150;
    this.sectionBlkBody.style.height = height + "px";
  }

  // Life cycle hooks
  //---------------------------------------------------------------------------------------------
  mounted() {
    this.setSectionBlkBodyHeight();
  }

  


}


