import { Icon } from './../../utilities/Icon';
import Vue from "vue";
import { Component } from "vue-property-decorator";
import * as Quill from "quill";
import * as utilities from "../../../modules/quill/helper";
import * as htmlElements from "../../../modules/HTMLElements";
import * as Mousetrap from 'mousetrap';


@Component({
  template: `
  <span class="icon-blk" ref="alignCenter">
    <Icon type="icon icon-align-center" width="60px">
      Center
      <br />
      (Alt + C)
    </Icon>
  </span>`,

  components: {
    Icon
  }
})

export class AlignCenter extends Vue {
  // Props
  //--------------------------------------------------------------------------------------------------------

  // Data Properties
  //--------------------------------------------------------------------------------------------------------

  // Computed Properties
  //--------------------------------------------------------------------------------------------------------

  // Methods
  //--------------------------------------------------------------------------------------------------------
  setup(quill: Quill.Quill, editorPage:HTMLElement) {
    const vm = this;
    const alignCenter = <HTMLElement> this.$refs.alignCenter;

    function _helper() {
      const selection = quill.getSelection();
      vm.toggleAlignCenter(quill, selection);
    }

    alignCenter.addEventListener("click", _helper );

    Mousetrap( editorPage ).bind(["alt+c"], () => {
      _helper();
      return false;
    })
  }

  toggleAlignCenter(quill: Quill.Quill, range:Quill.RangeStatic) {
    let format = quill.getFormat(range.index, range.length);
    if (format.align == "center") {
      delete format.align
      quill.removeFormat(range.index, range.length);
      quill.formatLine(range.index, range.length, format);
      return;
    }
    quill.formatLine(range.index, range.length, "align", "center");
  }


  // Watchers
  //--------------------------------------------------------------------------------------------------------

  // Life cycle hooks
  //--------------------------------------------------------------------------------------------------------
}



