import Vue from "vue";
import { Component, Watch } from "vue-property-decorator";
import * as Quill from 'quill';
import { Note } from '../../../database/queries/notes';
import * as Mousetrap from 'mousetrap';


@Component({
  template: `
  <div class="note-editor">
    <div class="note-editor--header">
      <input v-model="title" type="text" placeholder="Enter Note Title..."/>
    </div>
    <div class="note-editor--body">
      <div class="editor" ref="editor"></div>
    </div>
  </div>`,

  components: {
  }
})

export class NotesEditor extends Vue {
  // Props
  //--------------------------------------------------------------------------------------------------------

  // Data Properties
  //--------------------------------------------------------------------------------------------------------
  title: string = "";

  // Computed Properties
  //--------------------------------------------------------------------------------------------------------
  public get editor(): Quill.Quill {
    const quillEditor = <HTMLElement> this.$refs.editor;
    return new Quill( quillEditor, {
      placeholder: "Write something amazing...",
      modules: {
        syntax: true,
        //toolbar: [["code-block"]]
      }
    });
  }

  private get currentNote(): Note {
    return <Note> this.$store.getters["notesEditor/note"];
  }

  private get delta(): Quill.DeltaStatic {
    return this.currentNote.deltaContent
  }

  private get titleFromDatabase(): string {
    // Assign the title from the Database to the input, since computed properties are not reactive in inputs.
    return this.currentNote.title;
  }

  // Watchers
  //--------------------------------------------------------------------------------------------------------  
  @Watch("delta")
  onDeltaChanged() {
    this.editor.setContents( this.delta );
  }

  @Watch("titleFromDatabase")
  onTitleFromDatabaseChanged() {
    this.title = this.titleFromDatabase;
  }

  // Methods
  //--------------------------------------------------------------------------------------------------------
  noteTitleIsValidated():boolean {
    return ( this.title.length > 0 )
  }

  /**
   * updates the corrent 'note: Note' state of the editor with that of the veux store.
   * Returns true if update is successful. Return false if update fails. 
   */
  updateNote():boolean {
    if( this.noteTitleIsValidated() == false ) {
      // display error message to enter title.
      return false
    }

    const textContent = this.editor.getText();
    const deltaContent = this.editor.getContents();
    const note: Note = {
      title: this.title,
      textContent: textContent,
      deltaContent: deltaContent,
      settings: {},
    }
    this.$store.commit("notesEditor/setNote", note );
    return true;
  }
  
  public saveNote(currentCursorPosition: Quill.RangeStatic): Promise<any[]> {
    const vm = this;
    // If update fails: Immediately break out of the function.
    if( this.updateNote() == false ) return;
    const store = this.$store;
    return store
    .dispatch("notesEditor/saveNoteToDatabase")
    .then(function(): Promise<any[]> {
      // Reset current cursor position. This is because the save action moves the cursor position to
      // the top of the page.
      vm.editor.setSelection( currentCursorPosition.index, currentCursorPosition.length )
      return
    });
  }


  // Life cycle hooks
  //--------------------------------------------------------------------------------------------------------
  mounted() {
    // For the first time loaded
    const vm = this;
    vm.title = vm.titleFromDatabase;
    vm.editor.setContents( this.delta );

    // Bind Save functionality to Command + S or Ctrl + S
    const editorPage = <HTMLElement> document.querySelector(".ql-editor");
    Mousetrap( editorPage ).bind(["command+s", "ctrl+s"], function() {
      const currentCursorPosition: Quill.RangeStatic = vm.editor.getSelection();
      vm.saveNote( currentCursorPosition );
      return false;
    })

  }
}




