import Vue from "vue";
import { Component, Prop } from 'vue-property-decorator';
import { Icon } from "../../utilities/Icon";
import { Note, deleteNote } from '../../../database/queries/notes';
import { NotesEditor } from './NotesEditor';
import * as types from "moo-types";
import { SearchInput } from '../../utilities/SearchInput';
import { FindInEditor } from './FindInEditor';


@Component({
  template: `
    <div class="new-note--format-section">
      <span class="icon-container">
        <span @click="goBackToNoNotes">
          <Icon type="icon icon-arrow-circle-left" width="100px">Go Back</Icon>
        </span>
        <span @click="toggleToolbar">
          <Icon type="icon icon-tools" width="200px">Toggle editor toolbar</Icon>
        </span>
      </span>

      <span class="float-right icon-container">
        <FindInEditor />
        <span @click="saveNote" @mouseover="saveCurrentCursorPosition"><Icon type="icon icon-save">Save</Icon></span>
        <!--span><Icon type="icon icon-print">Print</Icon></span>
        <span><Icon type="icon icon-lock">Lock</Icon></span-->
        <span @click="deleteNote"><Icon type="icon icon-trash">Delete</Icon></span>
      </span>
    </div>`,

  components: {
    Icon,
    SearchInput,
    FindInEditor,
  }
})

export class FormatSection extends Vue {
  // Props
  //--------------------------------------------------------------------------------------------
  
  // Data Properties
  //---------------------------------------------------------------------------------------------
  currentCursorPosition: types.Optional<Quill.RangeStatic>

  // Computed Properties
  //---------------------------------------------------------------------------------------------
  get optNoteId(): types.Optional<number> {
    return this.$store.getters["notesEditor/currentNoteId"]
  }
  
  get notesEditor(): NotesEditor {
    return <NotesEditor> this.$parent.$refs.notesEditor
  }

  get notes() {
    return this.$store.getters["notesLibrary/libraryNotes"];
  }

  public get quillEditorDom():HTMLElement {
    return <HTMLElement> document.querySelector(".ql-editor");
  }


  // Methods
  //---------------------------------------------------------------------------------------------
  goBackToNoNotes(): void {
    if( this.notes.length > 0 ) {
      this.$store.commit("notesMainSection/setCurrentView", "NotesLibrary" );
    } else {
      this.$store.commit("notesMainSection/setCurrentView", "NoNotes" );
    }
  }

  getQuillEditorInstance(): Quill.Quill {
    return this.notesEditor.editor
  }

  /**
   * NOTE (Please Read)
   * --------------------------------------------------------- 
   * This method saves the current cursor position on mouseover on the save button.
   * This is because the default behavior is funny, so anytime a button is clicked,
   * the cursor immediately jumps somewhere else - mostly to the top of the page, and thus we're
   * unable to get the current cursor position before saving. So we save the cursor position anytime we
   * hover over the save button
  */
  saveCurrentCursorPosition() {
    const selection = this.notesEditor.editor.getSelection(true);
    this.currentCursorPosition = types.Optional.some(selection);
  }

  saveNote(e: MouseEvent) {
    const cursor = this.currentCursorPosition.force( <Quill.RangeStatic>{index:0, length:0} );
    this.notesEditor.saveNote( cursor );
  }

  deleteNote() {
    if( this.optNoteId.ifExist() == false ) return;
    const noteId = this.optNoteId.force(0);
    this.$store
    .dispatch("notesLibrary/deleteNoteFromDatabase", noteId )
    .then(() => {
      location.reload();
    })
  }

  toggleToolbar() {
    this.$store.commit("notesToolbar/toggleToolbar")
  }

  // Watchers
  //---------------------------------------------------------------------------------------------

  // Life cycle hooks
  //---------------------------------------------------------------------------------------------
  mounted() {
    // const vm = this;
    // const finder = <HTMLElement> vm.$refs.finder;
    // const queryPool = <HTMLElement> document.querySelector(".ql-editor");
    // console.log( queryPool );
    
    // finder.addEventListener("keyup", function(e) {
    //   vm.findQuery( vm.query, queryPool )
    // })

  }
}



