
/**
 * Determines the periodic offset of a query depending on the limit of queries and the query page.
 * @return {number}
 * @param {number} pageNo
 * @param {number} limit
*/
function calculateOffset( pageNo, limit) {
  return (pageNo - 1) * limit;
}


const helpers = {
  calculateOffset,
}

export default helpers;


