import { HasGetter, Mutation, VuexClass, extractVuexModule } from "vuex-class-module";


// Vuex Module Type Definitions
//-----------------------------------------------------------------------------------------------------
type Context = {
  state: State
  getters: Getters
  commit(mutation: string, payload: any): void
  dispatch(action: string, payload: any): void
}

type State = {
  isShown: boolean,
}

type Getters = {
  isShown: boolean
}


@VuexClass({
  modules: {}
})

class NotesToolbar {

  @HasGetter isShown: boolean = false;

  @Mutation toggleToolbar(state:State) {
    state.isShown = !state.isShown;
  }

}

const notesToolbar = extractVuexModule( NotesToolbar ); 

export default notesToolbar;


