import Vue from "vue";
import { Component } from "vue-property-decorator";

@Component({
  template: `
    <div class="side-nav">
      <ul>
        <li @click="showNotesBody" v-bind:class="{ active: notesIsActive }">
          <i class="icon-sticky-note"></i><span>Notes</span>
        </li>
        <!--li @click="showBookBody" v-bind:class="{ active: booksIsActive }">
          <i class="icon-book"></i><span>Books</span>
        </li>
        <li @click="showTodosBody" v-bind:class="{ active: todosIsActive }">
          <i class="icon-check"></i><span>Todos</span>
        </li>
        <li @click="showCalendarBody" v-bind:class="{ active: storeIsActive }">
          <i class="fontello fontello-shopping-bag"></i><span>Store</span>
        </li>
        <li><i class="icon-search"></i><span>Explore</span></li-->
      </ul>
    </div>`,

  components: {

  }
})

export class SideNav extends Vue {
  // Props

  // Data Properties

  // Computed Properties
  get editorTheme() {
    return this.$store.getters.editorTheme;
  }

  get notesIsActive() {
    return this.$store.getters["sideNav/notesIsActive"];
  }

  // get booksIsActive() {
  //   return this.$store.getters["sideNav/booksIsActive"];
  // }
  //
  // get todosIsActive() {
  //   return this.$store.getters["sideNav/todosIsActive"];
  // }
  //
  // get storeIsActive() {
  //   return this.$store.getters["sideNav/storeIsActive"];
  // }

  // Methods

  showNotesBody() {
    this.$store.commit("appBody/setCurrentView", "NotesBody")
  }
  // showBookBody() {
  //   this.$store.commit("appBody/setCurrentView", "BookBody")
  // }

  // Watchers

  // Life cycle hooks

}



