import Vue from "vue";
import { Component, Watch } from "vue-property-decorator";
import { ListWithIcon } from "../utilities/ListWithIcon";
import { SearchInput } from "../utilities/SearchInput";
import { NoNotes } from "./SideSection/NoNotes";
import { Notes } from './SideSection/Notes';
import { Icon } from '../utilities/Icon';

@Component({
  template: `
  <div class="side-section">
    <div class="section">

      <div class="search-input-blk">
        <SearchInput><input ref="noteSearcher" type="text" v-model="value" placeholder="Search Notes..." /></SearchInput>
      </div>

      <div class="side-section--notes section-blk">
        <!--ul class="section-blk--section-header">
          <li>All</li>
          <li>Latest</li>
          <li>Marked</li>
        </ul-->
        <ul class="section-blk--section-header">
          <li>Recent Notes</li>
          <span class="float-right" style="margin-top:10px;">
            <span @click="createNewNote"><Icon type="fontello-doc-new" width="100px">Add a new note</Icon></span>
          </span>
        </ul>

        <component :is="currentView">
          <NoNotes />
          <Notes />
        </component>
      
      </div>
    </div>
  </div>`,

  components: {
    ListWithIcon,
    SearchInput,
    NoNotes,
    Notes,
    Icon,
  }
})


export class SideSection extends Vue {
  // Props
  //-----------------------------------------------------------------------------------------------------------------

  // Data Properties
  //--------------------------------------------------------------------------------------------------------------------
  value:string = "";

  // Computed Properties
  //-------------------------------------------------------------------------------------------------------------------
  get editorThemeWithShade(): string {
    return this.$store.getters.editorTheme + " shade-level-2";
  }

  get currentView() {
    return this.$store.getters["notesSideSection/currentView"];
  }

  // Methods
  //----------------------------------------------------------------------------------------------------------------------
  createNewNote() {
    // Define an empty note.
    this.$store.commit("notesEditor/createUntitledNote");
    this.$store.commit( "notesMainSection/setCurrentView", "NewNote" );  
  }

  prepareToSearch( value:string) {
    if( value.length == 0 ) {
      this.$store.commit("notesLibrary/restoreLibrary");
    }
    if( value.length <= 2 ) return 
    this.$store.dispatch("notesLibrary/searchNotes", value )
  }

  // Watchers
  //-----------------------------------------------------------------------------------------------------------------------
 
  // Life cycle hooks
  //-----------------------------------------------------------------------------------------------------------------------
  mounted() {
    const vm = this;
    const noteSearcher = <HTMLElement> this.$refs.noteSearcher;
    
    noteSearcher.addEventListener("keyup", function(e) {
      vm.prepareToSearch( vm.value );
    });
  }
  
}



