import Vue from "vue";
import { Component } from "vue-property-decorator";
import { SideSection } from "./SideSection";
import { MainSection } from "./MainSection/MainSection";

@Component({
  template: `
    <div class="notes-body">
      <SideSection />
      <MainSection />
    </div>`,

    components: {
      SideSection,
      MainSection,
    }
})

export class NotesBody extends Vue {
  // Props

  // Data Properties

  // Computed Properties

  // Methods

  // Watchers

  // Life cycle hooks
  beforeCreate() {
    this.$store.dispatch("notesLibrary/loadLibraryNotes");
  }
  
}



