import * as Quill from "quill";

class Range {
  index: number;
  length: number;

  constructor(index: number, length: number) {
    this.index = index;
    this.length = length;
  }
}

export interface InlineBlot {
  new (): InlineBlot,
  domNode: HTMLElement,
  blotName: string,
  className: string,
  tagName: string,
  create(value?: any): HTMLElement,
  // format(name:string, value:any ):void
  // formats():Object,
  // formats(node?: HTMLElement):Quill.Formats,
}

const make = {
  Range: function (index: number, length: number): Quill.RangeStatic {
    return <Quill.RangeStatic>new Range(index, length)
  }
}

const quillJS = {
  make,
}

export default quillJS;



