/**
 * Converts hyphen case (eg. john-doe) string to title case (John Doe)
 * @returns {string} 
 * @param {string} str 
 */
function convertCamelCaseToTitleCase(str = "") {
  let result = "";
  str = str.split("-");
  str.forEach(function(val) {
    val = capitalizeFirstLetter(val);
    result += " " + val;
  });
  result = result.trim();
  return result;
}

/**
 * Removes all occurences of a substring from a string.
 * @returns {string}  
 * @param {string} str The string in which a substring is to be removed from. 
 * @param {string} substring The string to be removed. 
 */
function removeSubStringFromString( str, substring ) {
  return str.split( substring ).join("")
}

/**
 * Replaces all occurences of the "substring" in the "str" with a "replacement""
 * @returns {string} 
 * @param {string} str String to be searched and replaced on. 
 * @param {string} substring String to be replaced. 
 * @param {string} replacement Replacement string. 
 */
function replaceAll(str, substring, replacement) {
  return str.split( substring ).join( replacement );
}

/**
 * Capitalizes first letter in a string.
 * @returns {string} 
 * @param {string} str 
 */
function capitalizeFirstLetter(str) {
  const first = str[0].toUpperCase();
  const remaining = str.substring(1, str.length);
  const newString = first + remaining;
  return newString;
}


export default {
  convertCamelCaseToTitleCase,  
}


