import { Quill } from 'quill';
import Vue from "vue";
import { Component } from "vue-property-decorator";
import { Icon } from '../../utilities/Icon';
import * as utilities from "../../../modules/quill/helper";
import * as htmlElements from "../../../modules/HTMLElements";
import * as Mousetrap from 'mousetrap';

@Component({
  template: `
    <span class="icon-blk" ref="linkComponent">
      <Icon type="icon icon-link" width="100px">
        Link
        <br>
        (Ctrl/Cmd + L)
      </Icon>
    </span>`,

    components: {
      Icon,
    }
})

export class LinkComponent extends Vue {
  // Props
  //--------------------------------------------------------------------------------------------------------

  // Data Properties
  //--------------------------------------------------------------------------------------------------------

  // Computed Properties
  //--------------------------------------------------------------------------------------------------------

  // Methods
  //--------------------------------------------------------------------------------------------------------
  setup( quill: Quill.Quill, editorPage: HTMLElement ) {
    const vm = this;
    const link = <HTMLElement> this.$refs.linkComponent;
    let range: Quill.RangeStatic;

    // Define an helper function that can be reused 
    // for both click and keyboard command events.
    function _helper() {
      range = quill.getSelection();
      
      if( range == null || range == undefined ) return;

      const { format, formatExist } = utilities.inspectFormat(quill, range, "link");
          
      if( formatExist ) {
        utilities.removeFormat( quill, range, format, "link" );
        return;
      }
      // Show link overlay input.
      vm.$store.commit("overlayContainer/open", "LinkOverlay");
    }
        
    link.addEventListener("click", function() {
      _helper();
    });

    Mousetrap( editorPage ).bind(["command+l", "ctrl+l"], () => {
      _helper();
      return false;
    });

    vm.$root.$on("notesEditorLinkSet", function(value: string) {
      quill.formatText( range.index, range.length, "link", value );
    });

  }

  // Watchers
  //--------------------------------------------------------------------------------------------------------

  // Life cycle hooks
  //--------------------------------------------------------------------------------------------------------
}



