import Vue from "vue";
import { Component } from "vue-property-decorator";
import { Icon } from '../../utilities/Icon';
import * as Quill from "quill";
import * as utilities from "../../../modules/quill/helper";
import * as htmlElements from "../../../modules/HTMLElements";
import * as Mousetrap from 'mousetrap';


@Component({
  template: `
  <span class="icon-blk" ref="alignRight">
    <Icon type="icon icon-align-right" width="60px">
      Right
      <br />
      (Alt + R)
    </Icon>
  </span>`,

  components: {
    Icon
  }
})

export class AlignRight extends Vue {
  // Props
  //--------------------------------------------------------------------------------------------------------

  // Data Properties
  //--------------------------------------------------------------------------------------------------------

  // Computed Properties
  //--------------------------------------------------------------------------------------------------------

  // Methods
  //--------------------------------------------------------------------------------------------------------
  setup(quill: Quill.Quill, editorPage:HTMLElement ):void {
    const vm = this;
    const alignRight = <HTMLElement> this.$refs.alignRight;

    function _helper() {
      const range = quill.getSelection();
      const { format, formatExist } = utilities.inspectFormat(quill, range, "align");
      if (formatExist && format.align == "right") {
        utilities.removeFormat(quill, range, format, "align");
        return;
      }
      quill.formatLine(range.index, range.length, "align", "right");
    }

    alignRight.addEventListener("click", _helper);

    Mousetrap( editorPage ).bind(["alt+r"], () => {
      _helper();
      return false;
    })

  }

  // Watchers
  //--------------------------------------------------------------------------------------------------------

  // Life cycle hooks
  //--------------------------------------------------------------------------------------------------------
}



