/**_____________________________________________________________________________________
  component: DropDownMenu 
  ~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~
  This components is used to define the lists of items for any component with a drop down menu.
  This component has [ "commit", "value" ] prop.
  1). The "commit" prop is used to define the vuex mutation that will trigger when the drop down is clicked.
  2). The "value" prop is used to defined an accompanying value for the drop down menu.
  
  How to Use (Example)
  ~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~
  Any component that requires a drop down menu will use the DropDownMenu component to define the items
  that appear in the drop down menu.

  <InputWithDropDown>
    <DropDownMenu>11</DropDownMenu>
    <DropDownMenu>12</DropDownMenu>
    <DropDownMenu>13</DropDownMenu>
    <DropDownMenu>14</DropDownMenu>
    <DropDownMenu>15</DropDownMenu>
  </InputWithDropDown>

*/

import Vue from "vue";
import { Component } from "vue-property-decorator";

