import { Icon } from '../../utilities/Icon';
import Vue from "vue";
import { Component, Prop } from "vue-property-decorator"; 
import randomString from "randomstring";
import * as dates from "../../../modules/dates";

@Component({
  template: `
  <div class="note">
    <div class="note--inner" @click="innerNoteClicked"> 
      <i class="icon-sticky-note"></i>
      <span v-bind:title="title">{{ title }}</span>
    </div>
    <div class="note--footer">
      <!--span class="icon-blk"><Icon type="icon-font">Rename</Icon></span-->
      <span class="icon-blk" @click="deleteNote"><Icon type="icon-trash">Delete</Icon></span>
      <span class="right-bottom">{{ dateString }}</span>
      <!--span class="icon-blk"><Icon type="icon-settings">Settings</Icon></span-->
    </div>
  </div>`,
    
  components: {
    Icon,
  }
})

export class Note extends Vue {
  // Props
  //---------------------------------------------------------------------------------------------
  /**
   * Vue Prop: Defines the title of the book
   */
  @Prop public title: string;

  /**
   * Vue Prop: Defines the id of the book. used in the onclick event to open the editor.
   */
  @Prop public id: number;

  /**
   * Vue Prop: Defines the updatedAt date of the book. 
   */
  @Prop public updated: Date;
  

  // Data Properties
  //---------------------------------------------------------------------------------------------
  
  // Computed Properties
  //---------------------------------------------------------------------------------------------
  get dateString(): string {
    return this.updated.getDate() + "th " + dates.getMonth( this.updated ) + ", " + this.updated.getFullYear();
  }
  
  // Methods
  //---------------------------------------------------------------------------------------------
  innerNoteClicked() {
    this.$store.dispatch("notesEditor/openNoteInEditor", this.id );
  }

  deleteNote() {
    this.$store.dispatch("notesLibrary/deleteNoteFromDatabase", this.id );
  }

  // Watchers
  //---------------------------------------------------------------------------------------------
  
  // Life cycle hooks
  //---------------------------------------------------------------------------------------------
  mounted() {
  }

}




