import Vue from "vue";
import { Component, Prop } from 'vue-property-decorator';
import { Quill } from 'quill';
import * as htmlElements from "../../../modules/HTMLElements";
import { FormatSection } from "./FormatSection"
import { ToolbarSection } from "./ToolbarSection"
import { NotesEditor } from './NotesEditor';

@Component({
  template: `
    <div class="new-note" v-bind:style="{ minHeight: height }">
      <div class="new-note--header">
        <FormatSection />
        <ToolbarSection v-show="toolbarIsShown" />
      </div>
      <div class="new-note--body">
        <NotesEditor ref="notesEditor" />
      </div>
    </div>`,

    components: {
      FormatSection,
      ToolbarSection,
      NotesEditor,
    }
})

export class NewNote extends Vue {
  // Props
  //--------------------------------------------------------------------------------------------------------
  @Prop noteId: number 

  // Data Properties
  //--------------------------------------------------------------------------------------------------------
  editor: Quill;

  // Computed Properties
  //--------------------------------------------------------------------------------------------------------
  get height(): string {
    //return "770px";
    const elem = <HTMLElement> document.querySelector(".side-section");
    return htmlElements.getStyle( elem, "height")
  }

  get toolbarIsShown(): boolean {
    //return true;
    return this.$store.getters["notesToolbar/isShown"];
  }


  // Methods
  //--------------------------------------------------------------------------------------------------------

  // Watchers
  //--------------------------------------------------------------------------------------------------------

  // Life cycle hooks
  //--------------------------------------------------------------------------------------------------------
  mounted() {
    // console.log( this.$store.getters["notesEditor/note"] )
  }
}



