import Vue from "vue";
import { Component } from "vue-property-decorator";
import { Icon } from '../../utilities/Icon';
import * as Quill from "quill";
import * as types from "../../../modules/quill/type-maker";
import * as helper from "../../../modules/quill/helper";


@Component({
  template: `
    <span class="icon-blk" ref="horizontalRule">
      <Icon type="icon icon-measure">Horizontal Rule</Icon>
    </span>`,

    components: {
      Icon
    }
})

export class HorizontalRule extends Vue {
  // Props
  //--------------------------------------------------------------------------------------------------------

  // Data Properties
  //--------------------------------------------------------------------------------------------------------

  // Computed Properties
  //--------------------------------------------------------------------------------------------------------

  // Methods
  //--------------------------------------------------------------------------------------------------------
  setup( quill:Quill.Quill, editorPage:HTMLElement ) {
    const vm = this;
    const horizontalRule = <HTMLElement> this.$refs.horizontalRule;
    let range:Quill.RangeStatic

    function _helper() {
      range = quill.getSelection();
      vm.removeFormat( quill, range );
      quill.format("horizontal-rule", true );
    }

    horizontalRule.addEventListener("click", _helper );

    Mousetrap( editorPage ).bind(["command+r", "ctrl+r"], function() {
      _helper();
      return false;
    })

    Mousetrap( editorPage ).bind("del", function() { 
      vm.removeFormat( quill, range );
    });
  }

  removeFormat(quill:Quill.Quill, range:Quill.RangeStatic) {
    if( range == undefined ) return;

    const {format, formatExist} = helper.inspectFormat(quill, range, "horizontal-rule");
    if( formatExist ) {
      helper.removeFormat( quill, range, format, "horizontal-rule");
    }
  }


  // Watchers
  //--------------------------------------------------------------------------------------------------------

  // Life cycle hooks
  //--------------------------------------------------------------------------------------------------------
}



