import Vue from "vue";
import { Component } from "vue-property-decorator";
import { Icon } from "../../utilities/Icon";
import { Quill } from 'quill';

@Component({
  template: `
    <span class="icon-blk" ref="italic">
      <Icon type="icon icon-italic" width="100px">Italic <br>(Ctrl/Cmd + I)</Icon>
    </span>`,

    components: {
      Icon,
    }
})

export class Italic extends Vue {
  // Props
  //--------------------------------------------------------------------------------------------------------

  // Data Properties
  //--------------------------------------------------------------------------------------------------------

  // Computed Properties
  //--------------------------------------------------------------------------------------------------------

  // Methods
  //--------------------------------------------------------------------------------------------------------
  setup(quill: Quill.Quill, editorPage: HTMLElement): void {
    const italic = <HTMLElement> this.$refs.italic;
    italic.addEventListener("click", function() {
      const selection = quill.getSelection();
      quill.formatText( selection.index, selection.length, "italic", true );
    })
  }

  // Watchers
  //--------------------------------------------------------------------------------------------------------

  // Life cycle hooks
  //--------------------------------------------------------------------------------------------------------
}



