import Vue from "vue";
import { Component } from "vue-property-decorator";
import { Icon } from '../../utilities/Icon';
import * as Quill from "quill"
import * as utilities from "../../../modules/quill/helper";
import * as htmlElements from "../../../modules/HTMLElements";
import * as Mousetrap from 'mousetrap';

@Component({
  template: `
  <span class="icon-blk" ref="unorderedList">
    <Icon type="icon icon-list-ul">
      UnOrdered List
      <br/>
      (Alt + u)
    </Icon>
  </span>`,

  components: {
    Icon,
  }
})

export class UnorderedList extends Vue {
  // Props
  //--------------------------------------------------------------------------------------------------------

  // Data Properties
  //--------------------------------------------------------------------------------------------------------

  // Computed Properties
  //--------------------------------------------------------------------------------------------------------

  // Methods
  //--------------------------------------------------------------------------------------------------------
  setup(quill: Quill.Quill, editorPage: HTMLElement) {
    const unorderedList = <HTMLElement> this.$refs.unorderedList;
    let range: Quill.RangeStatic;

    function _helper() {
      range = quill.getSelection();
      const { format, formatExist } = utilities.inspectFormat(quill, range, "list");

      if (formatExist) {
        utilities.removeFormat(quill, range, format, "list");
        return;
      }

      quill.format("list", "unordered");
    }

    unorderedList.addEventListener("click", _helper);

    Mousetrap( editorPage ).bind(["alt+u"], () => {
      _helper();
      return false; 
    });
  }

  // Watchers
  //--------------------------------------------------------------------------------------------------------

  // Life cycle hooks
  //--------------------------------------------------------------------------------------------------------
}



