import Vue from "vue";
import { Component } from "vue-property-decorator";
import { Icon } from './../../utilities/Icon';
import * as Quill from "quill";
import * as utilities from "../../../modules/quill/helper";
import * as htmlElements from "../../../modules/HTMLElements";
import * as Mousetrap from 'mousetrap';


@Component({
  template: `
  <span class="icon-blk" ref="alignLeft">
    <Icon type="icon icon-align-left" width="60px">
      Left
      <br >
      (Alt + L)
    </Icon>
  </span>`,

  components: {
    Icon
  }
})

export class AlignLeft extends Vue {
  // Props
  //--------------------------------------------------------------------------------------------------------

  // Data Properties
  //--------------------------------------------------------------------------------------------------------

  // Computed Properties
  //--------------------------------------------------------------------------------------------------------

  // Methods
  //--------------------------------------------------------------------------------------------------------
  setup(quill: Quill.Quill, editorPage: HTMLElement): void {
    const vm = this;
    const alignLeft = <HTMLElement>this.$refs.alignLeft;

    function _helper() {
      const range = quill.getSelection();
      vm.toggleAlignLeft(quill, range);
    }

    alignLeft.addEventListener("click", _helper);

    Mousetrap( editorPage ).bind(["alt+l"], () => {
      _helper();
      return false;
    })

    //editorPage.bindKeys({ ctrlKey: true, shiftKey: true, key: "L" }, _helper);
  }

  toggleAlignLeft(quill: Quill.Quill, range: Quill.RangeStatic): void {
    let format = quill.getFormat(range.index, range.length);
    if (format.align == undefined) return;
    delete format.align;
    quill.removeFormat(range.index, range.length);
    quill.formatLine(range.index, range.length, format);
  }


  // Watchers
  //--------------------------------------------------------------------------------------------------------

  // Life cycle hooks
  //--------------------------------------------------------------------------------------------------------
}



