import * as Quill from "quill";
import { getAllRangesForQueryInPool, inspectFormat, removeFormat } from "./quill/helper";
import findAndReplaceDOMText from "findandreplacedomtext";

const MAX_CHARACTERS_ALLOWED_FOR_SIMPLE_SEARCH = 999999;

export const quillSelection = {

  foundStore: [] as Quill.RangeStatic[],

  revert(quill:Quill.Quill) {
    // If there's nothing in the found store, revert.
    if( this.foundStore.length <= 0 ) return
    // Go through each element in the this.foundStore and remove the format.
    this.foundStore.forEach( function(range:Quill.RangeStatic, index:number) {
      const { format, formatExist } = inspectFormat( quill, range, "find" )
      if( formatExist ) {
        removeFormat(quill, range, format, "find" )
        return;
      }
    })
  },

  find(quill:Quill.Quill, query:string):number {
    // Find all occurences of the query pattern
    let noOfMatchesFound = 0;
    const currentTextInTheDom = quill.getText();
    const rangeArr = getAllRangesForQueryInPool(query, currentTextInTheDom)
    // Update this.foundStore with the current list of selection.
    this.foundStore = rangeArr;

    // Attach a format to all occurences.
    rangeArr.forEach(function (range: Quill.RangeStatic, index: number) {
      noOfMatchesFound = index + 1;
      quill.formatText(range.index, range.length, "find", "find@" + noOfMatchesFound);
    })
    return noOfMatchesFound;
  },

}

export const stringReplaceSelection = {

  isEditorHighlighted: false,
  highlightedQuery: undefined as string,
  container: undefined as HTMLElement,
  newContainer:undefined as HTMLElement,
  parent: undefined,


  revert(query:string, container:HTMLElement, containerParent:HTMLElement) {
    query = query.trim();
    if( query.length == 0 ) return;

  
    if( this.container == undefined ) this.container = container;
    if( this.parent == undefined ) this.parent = containerParent;
    if( this.isEditorHighlighted == false ) return


    const replaced = this.replaceContent( this.container.innerHTML, `<span class="find">${ this.highlightedQuery }</span>`, this.highlightedQuery );
    const clone = this.cloneContainerDivWithReplacedContent(replaced);

    this.isEditorHighlighted = false;
    this.highlightedQuery = undefined;
    const currentChild = document.querySelector(".ql-editor");
    console.log( currentChild );
    this.parent.replaceChild( clone, this.newContainer );
    return

  },

  find(query:string) {
    query = query.trim();
    if (query.length == 0) return;

    // Create a regex match
    const replaced :string = this.replaceContent( this.container.innerHTML, query, `<span class="find">${query}</span>`);
    const clone = this.cloneContainerDivWithReplacedContent( replaced );
    this.parent.replaceChild( clone, this.container );

    // Make updates
    this.newContainer = clone; // Update parent since we've replaced the child
    this.container = clone;
    this.isEditorHighlighted = true;
    this.highlightedQuery = query;
    return clone
  },
  
  replaceContent(pool:string, found:string, replacing:string): string {
    const match = new RegExp(found, "ig");
    const replaced = pool.replace( match, replacing );
    return replaced;
  },

  cloneContainerDivWithReplacedContent(replacedContent:string):HTMLElement {
    const tmp = document.createElement( 'div' );
    tmp.className = "ql-editor";
    tmp.setAttribute("contenteditable", "true");
    tmp.setAttribute("data-placeholder", "Write something amazing...");
    tmp.innerHTML = replacedContent;
    return tmp;
  },

}

export const findAndReplaceDOMTextLibrary = {

  highlightedQuery: undefined as string,

  revert(query:string, container:HTMLElement) {
    if( this.highlightedQuery == undefined ) return;

    const highlightedNodes = document.querySelectorAll(".ql-editor span.find");
    [].forEach.call(highlightedNodes, function(node:HTMLSpanElement) {
      const text = node.innerText;
      const position: InsertPosition = "afterend";
      node.insertAdjacentText( position, text);
      node.remove();
    })    
  },

  find(query:string, container:HTMLElement) {
    const wrapElement = document.createElement("span")
    wrapElement.className = "find";
    
    findAndReplaceDOMText( container, {
      find: new RegExp( query, "ig" ),
      wrap: wrapElement,
    })

    this.highlightedQuery = query;

  },

}







