import Vue from "vue";
import { Component, Prop } from "vue-property-decorator";
import { LegendTag } from './LegendTag';

@Component({
  template: `
    <span class="icon-component">
      <i ref="icon"
        v-bind:type="type"
        v-bind:commit="commit"
        v-bind:class="icon"
        @click="handleClickEvent">
      </i>
      <div class="legend-tag-blk" v-bind:width="width" v-bind:style="{ width: width }">
        <LegendTag ref="legendTag"><slot></slot></LegendTag>
      </div>
    </span>`,

  components: {
    LegendTag,
  }
})

export class Icon extends Vue {
  // Props
  //---------------------------------------------------------------------------------------------
  @Prop 
  /**
   * Icon "Prop"" - defines the type of Icon. E.g <Icon type="icon icon-outline">[Legend String]</Icon> 
   */
  public type: string
  
  @Prop
  public commit: string
  
  @Prop
  public width: string

  // Data Properties
  //---------------------------------------------------------------------------------------------

  // Computed Properties
  //---------------------------------------------------------------------------------------------
  private get icon() {  
    return this.type; 
  }

  // Methods
  //---------------------------------------------------------------------------------------------
  handleClickEvent() {
    if( this.commit == undefined ) {
      return 
    }
    this.$store.commit( this.commit );
  }

  // Watchers
  //---------------------------------------------------------------------------------------------

  // Life cycle hooks
  //---------------------------------------------------------------------------------------------
  mounted() {
    const iconElement = <Element> this.$refs.icon ;  
    const legendTag = <LegendTag> this.$refs.legendTag;
  
    iconElement.addEventListener("mouseover", function() {
      legendTag.show();
    });
    
    iconElement.addEventListener("mouseout", function() {
      legendTag.hide();
    });
  }

}



