import * as Quill from 'quill';
import * as lf from "lovefield";

let schema = lf.schema.create("notex", 1 );

//    N O T E S   T A B L E
// -------------------------------------------------------------------------------------------------------------------------
// Define an exportable interface for Notes Table
export type NotesTableRow = {
  id: number,
  author: string,
  title: string,
  textContent: string,
  deltaContent: Quill.DeltaStatic,
  settings: object,
  createdAt: Date,
  updatedAt: Date,
};

// Create Notes Table.
schema.createTable("Notes")
  .addColumn("id", lf.Type.INTEGER )
  .addColumn("author", lf.Type.STRING )
  .addColumn("title", lf.Type.STRING )
  .addColumn("textContent", lf.Type.STRING )
  .addColumn("deltaContent", lf.Type.OBJECT )
  .addColumn("settings", lf.Type.OBJECT )
  .addColumn("updatedAt", lf.Type.DATE_TIME )
  .addColumn("createdAt", lf.Type.DATE_TIME )
  .addPrimaryKey(["id"], true );
//---------------------------------------------------------------------------------------------------------------------------



const connectToDB = schema.connect();
export default connectToDB;



