import * as notes from '../../database/queries/notes';
import * as types from "moo-types";
import * as Vue from 'vue';
import Delta from "quill-delta";
import { VuexClass, Getter, Mutation, Action, HasGetter, extractVuexModule } from "vuex-class-module";
import notesMainSection from './notesMainSection';
import { LibraryNote } from './notesLibrary';
import notesLibrary from './notesLibrary';
import general from '../general';
import { NotesTableRow } from '../../database/schema';


// Vuex Module Type Definitions
//-----------------------------------------------------------------------------------------------------
type Context = {
  state: State
  getters: Getters
  commit(mutation: string, payload?: any): void
  dispatch(action: string, payload?: any): void
}

type State = {
  currentNoteId: types.Optional<number>,
  currentLink: string,
  note: notes.Note,
  noteState: notes.NoteState,
}

type Getters = {
  currentNoteId: types.Optional<number>,
  currentLink: string,
  note: notes.Note,
  noteState: notes.NoteState,
}


@VuexClass({
  modules: {
    notesMainSection,
    notesLibrary,
    general,    
  }
})

class NotesEditor {
 // State
  //------------------------------------------------------------------------------------------------------------------
  @HasGetter currentNoteId = types.Optional.none<number>();
  
  @HasGetter currentLink = "" 
  
  @HasGetter note = {};    
  
  @HasGetter noteState = notes.NoteState.New; 
 //--------------------------------------------------------------------------------------------------------------------  


 // Mutations
  @Mutation createUntitledNote(state: State) {
    const note: notes.Note = {
      title: "",
      textContent: "",
      deltaContent: <Quill.DeltaStatic> new Delta(),
      settings: {},
    };
    state.note = note;
    state.noteState = notes.NoteState.New;
    state.currentNoteId = types.Optional.none<number>();
  }

  @Mutation setLink(state: State, link: string) {
    state.currentLink = link;
  }

  @Mutation setCurrentNoteId(state:State, noteId:types.Optional<number>) {
    state.currentNoteId = noteId;
  }

  @Mutation setNoteState(state: State, noteState: notes.NoteState) {
    state.noteState = noteState;
  }

  @Mutation setNote(state: State, note: notes.Note) {
    state.note = note;
  }
 //----------------------------------------------------------------------------------------------------------------------

 // Actions
  // ----------------------------------------------------------------------------------------------------------------------
  @Action saveNoteToDatabase(context: Context) {
    const noteState = context.getters.noteState;
    const optNoteId = context.getters.currentNoteId;
    //const anyGetter: Getters = context.getters
    
    // Update note if the note already exists in the DB.
    if (noteState === notes.NoteState.Old) {
      const noteId = optNoteId.force(0);
      notes
      // Update in the database.
      .updateNote(noteId, context.getters.note)
        // Update in the library.
      .then(() => { 
        context.dispatch("notesLibrary/refreshNotesFromDatabase");
      });
      return;
    }

    // Create new note if it doesn't exist in the database.
    if (noteState === notes.NoteState.New) {
      notes
      // Create a new untitled note in the database.
      .createNewNote(context.getters.note)
      .then((values) => {
        // Update the state of the note
        context.state.noteState = notes.NoteState.Old;
        context.dispatch("notesLibrary/refreshNotesFromDatabase");        
      });
      return
    }

  }

  @Action openNoteInEditor(context:Context, id:number) {
    notes.fetchNote( id )
    .then(function(values) {
      // Update State
      const value = values[0];
      context.commit("setCurrentNoteId", types.Optional.some<number>(value.id) )
      context.commit("setNoteState", notes.NoteState.Old );      
      context.commit("setNote", value );
    })
    .then(() => {
      context.commit("notesMainSection/setCurrentView", "NewNote")  
    })
  }
 //-----------------------------------------------------------------------------------------------------------------------

}

const notesEditor = extractVuexModule( NotesEditor )

export default notesEditor


