import * as Vue from 'vue';
import Vuex from "vuex";
import utilities from "../model/utilities/main.js";
import general from './general';
import appBody from "./appBody.js";
import overlayContainer from "./overlayContainer.js";
import notesMainSection from "./notes/notesMainSection";
import notesLibrary from './notes/notesLibrary';
import notesEditor from './notes/notesEditor';
import notesSideSection from './notes/notesSideSection';
import notesToolbar from './notes/notesToolbar';



//import "../mocker/electron-test";

// import * as mocker from "../mocker/notes-mocker";
// mocker.loadNotesToTheDatabase( 40 );


Vue.use( Vuex );

const store = new Vuex.Store({
  state: {
    isOverlayContainerShown: false,
    utilities: utilities,
    body: document.querySelector("body"),
  },
  mutations: {
    showOverlayContainer(state) {
      state.isOverlayContainerShown = true;
    },
    hideOverlayContainer(state) {
      state.isOverlayContainerShown = false;
    },
  },
  getters: {
    isOverlayContainerShown: state => state.isOverlayContainerShown,
    utilities: state => state.utilities,
    body: state => state.body,
  },
  modules: {
    general,
    appBody,
    overlayContainer,
    notesMainSection,
    notesEditor,
    notesLibrary,
    notesSideSection,
    notesToolbar,
  }
});




export default store;






