import Vue from "vue";
import { Component } from "vue-property-decorator";
import { Icon } from '../../utilities/Icon';
import * as Quill from "quill"
import * as utilities from "../../../modules/quill/helper";
import * as htmlElements from "../../../modules/HTMLElements";
import * as Mousetrap from 'mousetrap';

@Component({
  template: `
  <span class="icon-blk" ref="orderedList">
    <Icon type="icon icon-list-ol" width="100px">
      Ordered List
      <br />
      ( Alt + o )
    </Icon>
  </span>`,

  components: {
    Icon,
  }
})

export class OrderedList extends Vue {
  // Props
  //--------------------------------------------------------------------------------------------------------

  // Data Properties
  //--------------------------------------------------------------------------------------------------------

  // Computed Properties
  //--------------------------------------------------------------------------------------------------------

  // Methods
  //--------------------------------------------------------------------------------------------------------
  setup(quill: Quill.Quill, editorPage: HTMLElement) {
    const orderedList = <HTMLElement> this.$refs.orderedList;
    let range = undefined;

    function _helper() {
      range = quill.getSelection();
      const { format, formatExist } = utilities.inspectFormat(quill, range, "list");

      if (formatExist) {
        utilities.removeFormat(quill, range, format, "list");
        return;
      }

      quill.format("list", "ordered");
    }

    orderedList.addEventListener("click", _helper);

    Mousetrap( editorPage ).bind(["alt+o"], () => {
      _helper();
      return false; 
    })
    //htmlElements.bindKeys(editorPage, { ctrlKey: true, key: "k" }, _helper);
  }

  // Watchers
  //--------------------------------------------------------------------------------------------------------

  // Life cycle hooks
  //--------------------------------------------------------------------------------------------------------
}



