export function getMonth( month: Date | number ): string {
  //console.log( month instanceof Date  );
  let m: number;
  if( typeof month == "number" ) {
    m = month;
  } else if( month instanceof Date ) {
    m = month.getMonth() + 1;
  }

  let rtn = "unknown";
  switch(m) {
    case 1:
      rtn = "Jan"
      break;
    case 2:
      rtn = "Feb"
      break;
    case 3:
      rtn = "Mar"
      break;
    case 4:
      rtn = "Apr"
      break;
    case 5:
      rtn = "May"
      break;
    case 6:
      rtn = "June"
      break;
    case 7:
      rtn = "July"
      break;
    case 8:
      rtn = "Aug"
      break;
    case 9:
      rtn = "Sept"
      break;
    case 10:
      rtn = "Oct"
      break;
    case 11:
      rtn = "Nov"
      break;
    case 12:
      rtn = "Dec"
      break;
  }

  return rtn;
}


