import Vue from "vue";
import { Component } from "vue-property-decorator";
import { Icon } from '../../utilities/Icon';
import * as Quill from "quill"
import * as utilities from "../../../modules/quill/helper";
import * as htmlElements from "../../../modules/HTMLElements";
import * as Mousetrap from 'mousetrap';


@Component({
  template: `
  <span class="icon-blk" ref="indent">
    <Icon type="icon icon-indent" width="100px">
      Indent
      <br />
      (Alt + &#8594)
    </Icon>
  </span>`,

  components: {
    Icon,
  }
})

export class Indent extends Vue {
  // Props
  //--------------------------------------------------------------------------------------------------------

  // Data Properties
  //--------------------------------------------------------------------------------------------------------
  indentLimit: number = 8;

  // Computed Properties
  //--------------------------------------------------------------------------------------------------------

  // Methods
  //--------------------------------------------------------------------------------------------------------
  setup(quill: Quill.Quill, editorPage:HTMLElement) {
    const vm = this;
    const indent = <HTMLElement>this.$refs.indent;

    function _helper() {
      const selection = quill.getSelection();
      vm.indent(quill, selection);
    }

    indent.addEventListener("click", _helper);

    Mousetrap( editorPage ).bind(["alt+right"], () => {
      _helper();
      return false;
    })
  }

  indent(quill: Quill.Quill, range: Quill.RangeStatic) {
    if (range == null || range == undefined) return;

    const { indent } = quill.getFormat(range);

    if (indent == undefined) {
      quill.formatLine(range.index, range.length, "indent", 1);
      return;
    }

    if (indent == this.indentLimit) return;

    quill.formatLine(range.index, range.length, "indent", indent + 1);
  }


  // Watchers
  //--------------------------------------------------------------------------------------------------------

  // Life cycle hooks
  //--------------------------------------------------------------------------------------------------------
}



