/**_____________________________________________________________________________________
  component: DropDownMenuContainer
  ~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~
  This components provides a container with basic syling and behaviour for other components
  that require a drop down menu.
  This component has a [ 'width', 'height' ] prop. 
  1). The 'width' prop represents the width of the drop down menu container. 
  2). The 'height' prop represents the height of the drop down menu container. 
  
  How to Use (Example)
  ~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~
  Below is an instance where the DropDownMenuContainer is used.
  A 'v-show' directive is used to determine whether the drop down container is shown or not.
  This isShown variable can be determined as needed by the user of this component.
  The component usually contains a slot embeded in it where drop down menu lists are to be defined.
  using the DropDownMenu component. How this is defined is usually left to the user.

  <template>
    <span class="input-with-drop-down">
      <span class="text-input-blk">
        <TextInput />

        <DropDownMenuContainer 
          v-show="isShown" 
          :width="menuWidth" 
          :height="menuHeight">
          <slot></slot>
        </DropDownMenuContainer>
      </span>
    </span>
  </template>
*/


import Vue from "vue";
import { Component, Prop } from "vue-property-decorator";

@Component({
  template: `
  <div class="drop-down-menu-container" v-bind:style="{ width: width, height: height }">
    <slot></slot>
  </div>`,
    
  components: {
    
  }
})

export class DropDownMenuContainer extends Vue {
  // Props
  //---------------------------------------------------------------------------------------------
  @Prop width:string;
  @Prop height:string;
  
  // Data Properties
  //---------------------------------------------------------------------------------------------
  
  // Computed Properties
  //---------------------------------------------------------------------------------------------
  
  // Methods
  //---------------------------------------------------------------------------------------------
  
  // Watchers
  //---------------------------------------------------------------------------------------------
  
  // Life cycle hooks
  //---------------------------------------------------------------------------------------------
  
}


