import Vue from "vue";
import { Component } from "vue-property-decorator";
import { Icon } from '../../utilities/Icon';
import { Quill } from 'quill';

@Component({
  template: `
    <span class="icon-blk" ref="underline">
      <Icon type="icon icon-underline" width="100px">Underline <br>(Ctrl/Cmd + U)</Icon>
    </span>`,

    components: {
      Icon
    }
})

export class Underline extends Vue {
  // Props
  //--------------------------------------------------------------------------------------------------------

  // Data Properties
  //--------------------------------------------------------------------------------------------------------

  // Computed Properties
  //--------------------------------------------------------------------------------------------------------

  // Methods
  //--------------------------------------------------------------------------------------------------------
  setup(quill: Quill.Quill, editorPage: HTMLElement ) {
    const underline = <HTMLElement> this.$refs.underline;
    underline.addEventListener("click", function() {
      const selection = quill.getSelection();
      quill.formatText( selection.index, selection.length, "underline", true );
    })
  }

  // Watchers
  //--------------------------------------------------------------------------------------------------------

  // Life cycle hooks
  //--------------------------------------------------------------------------------------------------------
}



