import Vue from "vue";
import { Component } from "vue-property-decorator";
import { ButtonComponent } from "../../utilities/ButtonComponent";
import { Note, NoteState } from '../../../database/queries/notes';
import Delta from "quill-delta";


@Component({
  template: `
    <div class="no-notes">
      <div class="middle-blk">
        <div class="center-blk">
          <i class="icon-sticky-note"></i>
          <p style="font-size:2em;">Hey! You have no notes.</p>
          <p style="font-size:1.3em;">Lets do something about that.</p>
          <div class="button-component-blk" @click="addNewNote">
            <ButtonComponent color="silver">Add a new note</ButtonComponent>
          </div>
        </div>
      </div>
    </div>`,

    components: {
      ButtonComponent,
    }
})

export class NoNotes extends Vue {
  // Props
  //--------------------------------------------------------------------------------------------------------

  // Data Properties
  //--------------------------------------------------------------------------------------------------------

  // Computed Properties
  //--------------------------------------------------------------------------------------------------------

  // Methods
  //--------------------------------------------------------------------------------------------------------
  addNewNote() {
    // Define an empty note.
    this.$store.commit("notesEditor/createUntitledNote");
    this.$store.commit( "notesMainSection/setCurrentView", "NewNote" );
  }

  // Watchers
  //--------------------------------------------------------------------------------------------------------

  // Life cycle hooks
  //--------------------------------------------------------------------------------------------------------
}



