import Vue from "vue";
import { Component } from "vue-property-decorator";
import { NoNotes } from "./NoNotes";
import { NewNote } from "./NewNote";
import { NotesLibrary } from './NotesLibrary';
import * as notes from "../../../database/queries/notes";

@Component({
  template: `
    <div class="main-section" :theme="editorTheme">
      <component :is="currentView">
        <NoNotes />
        <NewNote v-bind:noteId="noteId" />
        <NotesLibrary />
      </component>
    </div>`,

  components: {
    NoNotes,
    NewNote,
    NotesLibrary,
  }
})

export class MainSection extends Vue {
  // Props
  //--------------------------------------------------------------------------------------------------------

  // Data Properties
  //--------------------------------------------------------------------------------------------------------

  // Computed Properties
  //--------------------------------------------------------------------------------------------------------
  get editorTheme(): string {
    return this.$store.getters.editorTheme
  }

  get currentView(): string {
    //return "NewNote";
    return this.$store.getters["notesMainSection/currentView"]
  }

  get noteId() {
    return this.$store.getters["notesEditor/currentNoteId"];
  }

  // Methods
  //--------------------------------------------------------------------------------------------------------

  // Watchers
  //--------------------------------------------------------------------------------------------------------

  // Life cycle hooks
  //--------------------------------------------------------------------------------------------------------
}



