  import Vue from "vue";
import { Component } from "vue-property-decorator";
import { Icon } from '../../utilities/Icon';
import * as Quill from "quill" 
import * as utilities from "../../../modules/quill/helper";
import * as htmlElements from "../../../modules/HTMLElements";
import * as Mousetrap from "mousetrap";

@Component({
  template: `
    <span class="icon-blk" ref="quote">
      <Icon type="icon icon-quote-right" width="100px">Quotes <br> (Ctrl/Cmd + Q)</Icon>
    </span>`,

    components: {
      Icon
    }
})

export class Quote extends Vue {
  // Props
  //--------------------------------------------------------------------------------------------------------

  // Data Properties
  //--------------------------------------------------------------------------------------------------------

  // Computed Properties
  //--------------------------------------------------------------------------------------------------------

  // Methods
  //--------------------------------------------------------------------------------------------------------
  setup( quill: Quill.Quill, editorPage: HTMLElement ) {
    const quote = <HTMLElement> this.$refs.quote;
    let range = undefined;

    function _helper() {
      range = quill.getSelection();
      const { format, formatExist } = utilities.inspectFormat( quill, range, "blockquote" );
      if( formatExist ) {
        utilities.removeFormat( quill, range, format, "blockquote" );
        return;
      }
      quill.formatLine( range.index, range.length, "blockquote", true );
    }

    quote.addEventListener("click", _helper );
    
    Mousetrap( editorPage ).bind(["command+q", "ctrl+q"], _helper );

  }
  
  // Watchers
  //--------------------------------------------------------------------------------------------------------

  // Life cycle hooks
  //--------------------------------------------------------------------------------------------------------
}



