//import { State, Mutation, Getters } from '../../modules/vuex-decorators';
import { HasGetter, Mutation, Getter, VuexClass, extractVuexModule } from "vuex-class-module";

// Vuex Module Type Definitions
//-----------------------------------------------------------------------------------------------------
type Context = {
  state: State
  getters: Getters
  commit(mutation: string, payload: any): void
  dispatch(action: string, payload: any): void
}

export type State = {
  currentView:string
}

export type Getters = {
  currentView:string
}

@VuexClass({
  modules: {}
})

class NotesSideSection {
  
  @HasGetter currentView:string = "NoNotes";

  @Mutation setCurrentView(state:State, view:string) {
    state.currentView = view;
  }

}

const notesSideSection = extractVuexModule( NotesSideSection );

export default notesSideSection




