import { Mutation, VuexClass, HasGetter, extractVuexModule } from 'vuex-class-module';
import * as notes from  "../../database/queries/notes";

// Type Definitions
//------------------------------------------------------------------
type State = {
  currentView: string
}

interface Getters {
  currentView(state:State): string,
};

interface Context {
  state: State,
  getters: Getters,
  commit(mutation: string, payload: any): void,
  dispatch(action: string, payload: any): void
}
//---------------------------------------------------------------------


@VuexClass({
  modules: {}
})

class NotesMainSection {

  @HasGetter currentView:string = "NoNotes";

  @Mutation setCurrentView(state:State, view:string) {
    state.currentView = view;
  }

}

const notesMainSection = extractVuexModule( NotesMainSection );

export default notesMainSection


