import { Alert } from './utilities/Alert';
import Vue from "vue";
import { Component } from "vue-property-decorator";
import { LinkOverlay } from './quill/custom/LinkOverlay';
import { RenameNoteOverlay } from './notes/RenameNoteOverlay';

@Component({
  template: `
    <div class="overlay-container">
      <div class="overlay-content">
        <component :is="innerView">
          <NewBookOverlay />
          <PageSettingsOverlay />
          <LinkOverlay />
          <RenameNoteOverlay />
          <Alert />
        </component>
      </div>
    </div>`,

  components: {
    LinkOverlay,
    RenameNoteOverlay,
    Alert,
  }
})

export class OverlayContainer extends Vue {
  // Props

  // Data Properties

  // Computed Properties
  get innerView(): string {
    //return "Alert";
    return this.$store.getters["overlayContainer/innerView"] 
  }

  // Methods

  // Watchers

  // Life cycle hooks

}



