const overlayContainer = {
  namespaced: true,
  state: {
    isShown: false,
    innerView: undefined,
  },
  mutations: {
    open(state, innerView) {
      state.innerView = innerView;
      state.isShown = true;
    },
    close(state) {
      state.isShown = false;
      state.innerView = undefined;
    },
  },
  actions: {
    
  },
  getters: {
    isShown:(state)=> state.isShown,
    innerView:(state)=> state.innerView,
  },
}


export default overlayContainer


