

const appBody = {
  namespaced: true,
  state: {
    currentView: "NotesBody",
  },
  mutations: {
    /**
     * @param {string} view Represents the current view seen in the App Body which can either be: "BookBody", "NotesBody", "TodosBody", "StoreBody", "ExploreBody". 
     */
    setCurrentView(state, view) {
      state.currentView = view;
    }
  },
  actions: {},
  getters: {
    currentView:(state) => state.currentView,
  },
}



export default appBody


