import * as Quill from "quill";
import * as types from "./type-maker";


// Define an inline Blot Format.
let Inline = Quill.import("blots/inline");
let BlockEmbed = Quill.import("blots/block/embed");

// Register Find Format to Quill once.
//----------------------------------------------------------------------------------------------------
class Find extends Inline {

  static create(value) {
    let node = super.create(value);
    node.setAttribute("id", value);
    return node;
  }

  formats() {
    let formats = super.formats();
    formats['find'] = Find.formats(this.domNode);
    return formats;
  }

  static formats(domNode) {
    return domNode.getAttribute('id');
  }

  format(name, value) {
    if (name === "find" && value) {
      this.domNode.setAttribute("id", value);
    } else {
      super.format(name, value);
    }
  }

}


Find.blotName = "find";
Find.className = "find";

Quill.register({
  "formats/find": Find,
})
//--------------------------------------------------------------------------------------------------------------


// Register Horizontal Rule Format to Quill Once.
//--------------------------------------------------------------------------------------------------------------
class HorizontalRule extends BlockEmbed {}
HorizontalRule.blotName = "horizontal-rule";
HorizontalRule.tagName = "hr";

Quill.register({
  "formats/horozontal-rule": HorizontalRule,
})
//--------------------------------------------------------------------------------------------------------------


