import Vue from "vue";
import { Quill } from 'quill';
import { Component, Watch } from "vue-property-decorator";
import * as htmlElements from "../../../modules/HTMLElements";

import { Bold } from '../../quill/custom/Bold';
import { Italic } from '../../quill/custom/Italic';
import { Underline } from '../../quill/custom/Underline';
import { LinkComponent } from '../../quill/custom/LinkComponent';
import { Quote } from '../../quill/custom/Quote';
import { CodeComponent } from "../../quill/custom/CodeComponent";
import { HeaderComponent } from "../../quill/custom/HeaderComponent";
import { HorizontalRule } from '../../quill/custom/HorizontalRule';
import { BackgroundColor } from "../../quill/custom/BackgroundColor";
import { Color } from "../../quill/custom/Color";
import { OrderedList } from '../../quill/custom/OrderedList';
import { UnorderedList } from '../../quill/custom/UnorderedList';
import { Indent } from '../../quill/custom/Indent';
import { Outdent } from '../../quill/custom/Outdent';
import { AlignLeft } from '../../quill/custom/AlignLeft';
import { AlignRight } from '../../quill/custom/AlignRight';
import { AlignCenter } from '../../quill/custom/AlignCenter';
import { AlignJustify } from '../../quill/custom/AlignJustify';
import { ImageComponent } from "../../quill/custom/ImageComponent";
import { VideoComponent } from "../../quill/custom/VideoComponent";
import { NotesEditor } from './NotesEditor';

@Component({
  template: `
    <div class="new-note--toolbar-section" ref="toolbar" v-bind:style="styles">
      <div class="center">
        <span class="tl-sections">
          <Bold ref="bold" />
          <Italic ref="italic" />
          <Underline ref="underline" />
        </span>
        <span class="tl-sections">
          <LinkComponent ref="linkComponent" />
          <Quote ref="quote" />
          <CodeComponent ref="codeComponent" />
        </span>
        <span class="tl-sections">
          <HeaderComponent ref="headerComponent" />
          <HorizontalRule ref="horizontalRule" />
        </span>
        <!--span class="tl-sections">
          <BackgroundColor />
          <Color />
        </span-->
        <span class="tl-sections">
          <OrderedList ref="orderedList" />
          <UnorderedList ref="unorderedList" />
        </span>
        <span class="tl-sections">
          <Indent ref="indent" />
          <Outdent ref="outdent" />
        </span>
        <span class="tl-sections">
          <AlignLeft ref="alignLeft" />
          <AlignRight ref="alignRight" />
          <AlignCenter ref="alignCenter" />
          <AlignJustify ref="alignJustify" />
        </span>
        <!--span class="tl-sections">
          <ImageComponent />
          <VideoComponent />
        </span-->
      </div>
    </div>`,

  components: {
    Bold,
    Italic,
    Underline,
    LinkComponent,
    Quote,
    CodeComponent,
    HeaderComponent,
    HorizontalRule,
    BackgroundColor,
    Color,
    OrderedList,
    UnorderedList,
    Indent,
    Outdent,
    AlignLeft,
    AlignRight,
    AlignCenter,
    AlignJustify,
    ImageComponent,
    VideoComponent,
  }
})

export class ToolbarSection extends Vue {
  // Props
  //--------------------------------------------------------------------------------------------------------

  // Data Properties
  //--------------------------------------------------------------------------------------------------------
  styles: object = {};
  toolbarOffsetFromTheTop: number = undefined;

  // Computed Properties
  //--------------------------------------------------------------------------------------------------------
  get toolbar(): HTMLElement {
    return this.$refs.toolbar as HTMLElement;
  }

  get toolbarIsShown(): boolean {
    return this.$store.getters["notesToolbar/isShown"];
  }

  // Watchers
  //--------------------------------------------------------------------------------------------------------
  @Watch("toolbarIsShown")
  whenToolbarIsShownChanges() {
    this.updateToolbarOffsetFromTheTop();
  }

  // Methods
  //--------------------------------------------------------------------------------------------------------
  addFixableToolbarStyles() {
    const fixableStyles = {
      position: "fixed",
      top: "0",
      zIndex: "9999",
      backgroundColor: "rgba(255,255,255,0.9)",
      width: htmlElements.getStyle( this.toolbar, "width"),
    }
    this.styles = fixableStyles;
  }

  removeFixableToolbarStyles() {
    this.styles = {};
  }

  updateToolbarOffsetFromTheTop() {
    this.toolbarOffsetFromTheTop = this.toolbar.offsetTop
  }

  // Life cycle hooks
  //--------------------------------------------------------------------------------------------------------
  mounted() {
    const vm = this;
    const notesEditor = <NotesEditor> vm.$parent.$refs.notesEditor;
    const quill = notesEditor.editor;
    const editorPage = <HTMLElement> document.querySelector(".ql-editor");

    setup_toolbar_buttons: {
      const bold = <Bold>this.$refs.bold;
      bold.setup(quill, editorPage);

      const italic = <Italic>this.$refs.italic;
      italic.setup(quill, editorPage);

      const underline = <Underline>this.$refs.underline;
      underline.setup(quill, editorPage);

      const linkComponent = <LinkComponent>this.$refs.linkComponent;
      linkComponent.setup(quill, editorPage);

      const quote = <Quote>this.$refs.quote;
      quote.setup(quill, editorPage);

      const codeComponent = <CodeComponent>this.$refs.codeComponent;
      codeComponent.setup(quill, editorPage);

      const horizontalRule = <HorizontalRule> this.$refs.horizontalRule;
      horizontalRule.setup(quill, editorPage);

      const headerComponent = <HeaderComponent> this.$refs.headerComponent;
      headerComponent.setup( quill, editorPage );

      const orderedList = <OrderedList>this.$refs.orderedList;
      orderedList.setup(quill, editorPage);

      const unorderedList = <UnorderedList>this.$refs.unorderedList;
      unorderedList.setup(quill, editorPage);

      const indent = <Indent>this.$refs.indent;
      indent.setup(quill, editorPage);

      const outdent = <Outdent>this.$refs.outdent;
      outdent.setup(quill, editorPage);

      const alignLeft = <AlignLeft>this.$refs.alignLeft;
      alignLeft.setup(quill, editorPage);

      const alignRight = <AlignRight>this.$refs.alignRight;
      alignRight.setup(quill, editorPage);

      const alignCenter = <AlignCenter>this.$refs.alignCenter;
      alignCenter.setup(quill, editorPage);

      const alignJustify = <AlignJustify>this.$refs.alignJustify;
      alignJustify.setup(quill, editorPage);
    }

    handle_fixing_of_toolbar_when_scrolling: {
      // Update offset from top immediatele page loads.
      vm.updateToolbarOffsetFromTheTop();

      window.addEventListener("scroll", function (e) {
        // Only run this function if toolbar is displayed.
        if (!vm.toolbarIsShown) return;

        if (this.pageYOffset >= vm.toolbarOffsetFromTheTop) {
          vm.addFixableToolbarStyles();
        } else {
          vm.removeFixableToolbarStyles();
        }
      })
    }
  }

}



